.recent-activities {
    .container {

        padding-bottom: 30px;
    }

    &__title {
        padding-top: 72px;
        text-align: center;

        h2 {
            font-weight: 500;
            margin-bottom: 6px;
            font-size: 21px;
            line-height: 26px;
            color: #d32323;
        }
    }

    &__list {
        display: flex;
        margin: 0 -15px
    }

    &__col {
        flex: 0 0 33.33333%;
        max-width: 33.33333%;
        padding: 0 15px;

    }

    &__item {
        margin-top: 30px;
        padding: 11px;
        border: 1px solid #e6e6e6;
        border-radius: 4px;

        &__title {
            padding-bottom: 11px;
            display: flex;
            align-items: center;
            border-bottom: 1px solid #e6e6e6;

            &__image {
                margin-left: 12px;

                img {
                    width: 30px;
                    height: 30px;
                    border-radius: 4px;
                }
            }

            &__text {
                display: flex;
                flex-direction: column;

                >a {
                    font-weight: 500;
                    color: #0073bb;

                    &:hover {

                        text-decoration: underline;
                    }
                }

                span {}

            }
        }

        &__content {


            &__image {
                >img {
                    // width: 286px;
                    height: 150px;
                    max-width: 100%;

                }
            }

            &__title {
                padding-top: 12px;
                margin-bottom: 12px;



                >a {
                    color: #0073bb;
                    font-weight: 500;

                    span {}
                }
            }

            &__like {
                padding: 8px 6px;
                margin-top: 0;
                border: 1px solid #e6e6e6;
                border-top: 0 solid #e6e6e6;
                border-bottom-right-radius: 3px;
                border-bottom-left-radius: 3px;


                >a {
                    color: #999;
                    display: flex;
                    align-items: center;

                    >svg {
                        fill: #999;
                        margin-left: 5px;
                        position: relative;
                        top: -2px;

                    }

                    &:hover {
                        color: #0073bb;

                        svg {
                            fill: #0073bb;
                        }


                    }
                }
            }
        }

        &__image {
            display: flex;
            flex-wrap: wrap;
            height: 374px;
            overflow: hidden;
            margin: 6px -3px 0;
                &--active
                    {
                        height: 445px;
                        margin-bottom: -10px;
                        overflow-y: auto;
                        overflow-x: hidden;
                    }
            

            &__item {
                $item: &;
                padding: 0 3px;
                flex: 0 0 50%;
                margin-bottom: 10px;

                &__img {
                    height: 132px;
                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        border-radius: 4px 4px 0 0;
                    }
                }

                &__like {
                    padding: 8px 6px;
                    margin-top: 0;
                    border: 1px solid #e6e6e6;
                    border-top: 0 solid #e6e6e6;
                    border-radius: 0 0 3px 3px;

                    >a {
                        color: #999;
                        display: flex;
                        align-items: center;

                        >svg {
                            fill: #999;
                            margin-left: 5px;
                            position: relative;
                            top: -2px;

                        }

                        &:hover {
                            color: #0073bb;

                            svg {
                                fill: #0073bb;
                            }


                        }
                    }
                }
                &--big
                {
                    flex: 0 0 100%;
                    #{$item}__img
                    {
                        height: 150px;
                    }
                }
                
            }
        }

        &__show-all {
            padding: 8px 0 0;
            text-align: center;

            >a {
                color: #0073bb;
                font-size: 12px;

                &:hover {
                    text-decoration: underline;
                }
            }
        }


    }

