@media(max-width: 768px)
{
    .recent-activities
    {
        &__col
        {
            flex: 0 0 50%;
            max-width: 50%;
        }
    }
}


@media(max-width:576px) {
    .recent-activities {
        display: none;
    }
}

@media(max-width: 480px) {
    .recent-activities {
        display: none;
    }
}