.header-type4 {
	box-shadow: 0 2px 4px rgba(0, 0, 0, .08);
	background-color: #fff;
	position: fixed;
	z-index: 999;
	left: 0;
	top: 0;
	right: 0;

	.container,
	.container-fluid {
		display: flex;
		align-items: center;
		padding: 0 20px;
		height: 87px;
	}

	&__logo {
		display: flex;
		align-items: center;

		img {
			height: 40px;
		}

		span {
			font-size: 20px;
			line-height: 26px;
			font-weight: 400;
			color: #2b273c;
			margin-right: 12px;


		}
	}

	&__nav {
		margin-right: auto;
		margin-left: 60px;
		>ul {
			display: flex;
			align-items: center;

			>li {
				position: relative;
				margin-left: 60px;
				>a {
					color: rgba(0,0,0,0.8);
					font-size: 12px;
					padding: 0 5px;
					line-height: 30px;
					display: block;
				}
				&:hover,&.active {
					>a {
						color: rgb(238, 41, 41);
					}
				}
				&:last-child
				{
					margin-left: 0;
				}
			}
		}
	}

	&__search
	{
		position: relative;
		flex: 0 0 310px;
		form
		{
			position: relative;
		}
		input
		{
			line-height: 30px;
			border: 1px solid rgba(0, 0, 0, 0.8);
			border-radius: 16px;
			padding: 0 45px 0 15px;
			background-color: transparent;
			width: 100%;
		}
		button
		{
			position: absolute;
			right: 5px;
			top: 0;
			border: transparent;
			background-color: transparent;
			padding: 0;
			margin: 0;
			width: 32px;
			height: 32px;
			display: flex;
			align-items: center;
			justify-content: center;
			text-align: center;
			line-height: 30px;
			svg
			{
				position: absolute;
				top: 50%;
				right: 50%;
				transform: translate(50%,-50%);
			}
		}
	}

	
}



@media(max-width:1250px)
{
	.header-type4
	{
		&__nav
		{
			margin-left: 30px;
			>ul
			{
				>li
				{
					margin-left: 30px;
				}
			}
		}
		&__search
		{
			flex: 0 0 250px;
		}
	}
}
@media(max-width:992px)
{
	.header-type4
	{
		display: none;
	}
}