.page-restaurant{
    padding: 104px 0;
	margin-bottom: 75px;
    .container {
        max-width: 1174px;
	}
	&__title
	{
		text-align: center;
		margin-bottom: 72px;
		h2
		{
			color: #2b273c;
			font-size: 28px;
			line-height: 36px;
			font-weight: 500;
		}
	}
	&__item
	{
		display: flex;
		flex-direction: column;
		border: 1px solid #efefef;
		border-radius: 4px;
		padding: 20px;
		margin-bottom: 32px;
		color: #2b273c;
		height: calc(100% - 32px);
		&__header
		{
			display: flex;
			margin-bottom: 12px;
			&__image
			{
				flex: 0 0 68px;
				max-width: 68px;
				height: 68px;
				border-radius: 4px;
				position: relative;
				overflow: hidden;
				img
				{
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
			&__content
			{
				flex: 0 0 calc(100% - 68px);
				max-width: calc(100% - 68px);
				padding-right: 12px;
				h2
				{
					font-weight: 600;
					font-size: 16px;
					color: #2b273c;
				}
				span
				{
					display: block;
					line-height: 16px;
					color: #757280;
					margin-top: 4px;
				}
			}
		}
		&__content
		{
			p
			{
				font-size: 16px;
				line-height: 25px;
				color: #2b273c;
				margin-bottom: 7px;
			}
			span
			{
				font-size: 12px;
				line-height: 16px;
				font-style: normal;
				color: #757280;
				display: block;
			}
		}
	}

}
