.category-content {
	position: relative;
	margin-left: 32%;
	transition: 0.3s;
	z-index: 90;
	padding: 0 40px;
	display: flex;

	&__map {
		position: fixed;
		top: 153px;
		bottom: 0;
		left: 0;
		z-index: 90;
		width: 32%;
		transition: 0.3s;
	}

	&__sidebar {
		flex: 0 0 248px;
		max-width: 248px;
		padding-top: 24px;
	}

	&__item-filter {

		position: -webkit-sticky;
		position: sticky;
		top: 177px;
		transition: 0.3s;
		padding-left: 40px;

		&__filter {

			border-bottom: 1px solid #eeeeef;

			h3 {
				color: #2b273c;
				font-size: 16px;
				line-height: 22px;
				font-weight: 500;
			}

			&__number {
				padding: 16px 0;

				ul {
					display: flex;
					align-items: center;
					border: 1px solid #bbbac0;
					border-radius: 16px;
					width: 125px;


					li {
						border-left: 1px solid #bbbac0;
						padding: 6px 12px;



						&:last-child {
							border-left: none;
						}

						a {
							font-size: 12px;
							line-height: 18px;
							font-weight: 500;
							color: #2b273c;

							&:hover {
								background-color: #e2e2e6;
							}

						}
					}
				}

			}


		}

		&__proposal {
			padding: 16px 0;
			border-bottom: 1px solid #eeeeef;

			&__title {
				h3 {
					color: #2b273c;
					font-size: 14px;
					line-height: 20px;
					font-weight: 500;
					margin-bottom: 8px;
				}
			}

			.span {
				span {
					color: #757280;
				}
			}
		}

		&__property {
			padding: 16px 0;
			border-bottom: 1px solid #eeeeef;

			&__title {
				h3 {
					color: #2b273c;
					font-size: 14px;
					line-height: 20px;
					font-weight: 500;
					margin-bottom: 8px;
				}
			}

			&__all {
				padding-top: 4px;

				a {
					color: #00838f;
					font-size: 12px;
					line-height: 18px;
					font-weight: 500;

					&:hover {
						text-decoration: underline;
					}
				}
			}


		}

		&__areas {
			padding: 16px 0;
			border-bottom: 1px solid #eeeeef;

			&__title {
				h3 {
					color: #2b273c;
					font-size: 14px;
					line-height: 20px;
					font-weight: 500;
					margin-bottom: 8px;
				}
			}

			&__all {
				padding-top: 4px;

				a {
					color: #00838f;
					font-size: 12px;
					line-height: 18px;
					font-weight: 500;

					&:hover {
						text-decoration: underline;
					}
				}
			}
		}

		&__distance {
			padding: 16px 0;


			&__title {
				h3 {
					color: #2b273c;
					font-size: 14px;
					line-height: 20px;
					font-weight: 500;
					margin-bottom: 8px;
				}
			}

		}
    }
    
    &__filter-mobail {
        display: none;
        align-items: center;
        justify-content: space-between;
        background-color: #f5f5f5;
        padding: 10px;
        position: fixed;
        left: 0;
        right: 0;
        top: 153px;
        transition: 0.3s;
        z-index: 99;
        &__button-filter {
            a {
                padding: 0px 8px;
                border-radius: 3px;
                font-size: 13px;
                line-height: 29px;
                color: #666;
                display: flex;
                background: #fff;
                border: 1px solid #ccc;
                &.active
                {
                    background-color: #eee;
                }
            }
        }

        &__button-list {
            
            
            display: flex;
            align-items: center;

            &__list {
                margin-left: -1px;
                a {
                    padding: 0px 8px;
                    border-radius: 0 3px 3px 0;
                    font-size: 13px;
                    line-height: 29px;
                    color: #666;
                    display: flex;
                    background: #fff;
                    border: 1px solid #ccc;
                    &.active
                    {
                        background-color: #eee;
                    }
                }
            }

            &__map {
                a {
                    padding: 0px 8px;
                    border-radius: 3px 0 0 3px;
                    font-size: 13px;
                    line-height: 29px;
                    color: #666;
                    display: flex;
                    background: #fff;
                    border: 1px solid #ccc;
                    &.active
                    {
                        background-color: #eee;
                    }
                }
            }
        }
    }

	&__item {
		flex: 0 0 calc(100% - 248px);
		max-width: calc(100% - 248px);

		&__title {
			margin-top: 24px;
			margin-bottom: 24px;

			&__top {
				a {
					color: #757280;
				}
			}

			&__bottom {
				display: flex;
				align-items: center;

				&__right {
					h1 {
						color: #2b273c;
						font-weight: 500;
						font-size: 24px;
						line-height: 30px;
					}

				}

				&__left {
					margin-right: auto;
					display: flex;
					align-items: center;
					padding: 10px 0;

					span {
						// margin-left: 4px;
						color: #2b273c;
						font-weight: 400;
						font-size: 14px;
						line-height: 17px;
						width: auto;
						margin-left: 6px;

					}

					select {}

					option {
						color: #2b273c;
						font-weight: 500;
						font-size: 14px;
						line-height: 17px;
						// margin-left: 4px;
					}

					.bootstrap-select .dropdown-toggle .filter-option {
						padding-left: 10px;
					}



					.bootstrap-select.form-control:not([class*=col-]) {
						width: unset;
					}

					.bootstrap-select:not(.input-group-btn),
					.bootstrap-select[class*=col-] {
						display: flex;
						align-items: center;
					}

					.btn {
						padding: 0;
					}

					.bootstrap-select .dropdown-menu {
						box-shadow: 0 0 18px rgba(0, 0, 0, .15);
					}

					.bootstrap-select>.dropdown-toggle {
						background-color: transparent;
						border: transparent;

					}

					.bootstrap-select>.dropdown-toggle:hover .filter-option-inner-inner {
						text-decoration: underline;

					}

					.btn-light:not(:disabled):not(.disabled).active,
					.btn-light:not(:disabled):not(.disabled):active,
					.show>.btn-light.dropdown-toggle {
						color: #2b273c;
						background-color: transparent;
						border-color: transparent;
						border: none;
						outline: none;
						box-shadow: none;

					}

					.bootstrap-select .dropdown-toggle:focus,
					.bootstrap-select>select.mobile-device:focus+.dropdown-toggle {
						outline: none !important;
						box-shadow: none;
					}

					.filter-option-inner-inner {
						text-align: right;
						font-weight: 400;
						font-size: 14px;
						line-height: 20px;
					}

					.dropdown-menu {
						.inner {
							.show {
								text-align: right;
							}
						}

						.selected {
							&.active {
								background-color: #e6f7fa;
							}
						}

					}
				}

			}
		}

		&__cat-product {
			border: 1px solid #eeeeef;
			border-radius: 4px;
			transition: 0.3s;
			padding: 24px;
			display: flex;
			margin-bottom: 24px;


			&:hover {
				box-shadow: 0 0 10px rgba(0, 0, 0, .1);
			}

			&__image {

				flex: 0 0 200px;
				max-width: 200px;
                height: 200px;
				img {
					width: 100%;
					height: 100%;
					border-radius: 4px;

				}
			}

			&__content {
				flex: 0 0 calc(100% - 200px);
				max-width: calc(100% - 200px);
				padding-right: 24px;

				&__text {
					display: flex;



					&__desc {
						flex: 0 0 240px;
						max-width: 240px;

						&__title {

							margin-bottom: 12px;

							a {
								h4 {
									color: #2b273c;
									font-weight: 500;
									font-size: 14px;
									line-height: 20px;
								}

								&:hover {
									text-decoration: underline;
								}
							}
						}

						&__icon {
							display: flex;
							align-items: center;
							margin-bottom: 12px;

							&__star {
								margin-left: 8px;
								width: 108px;
								height: 20px;
								background-image: url(../img/star.png);
								background-repeat: no-repeat;

								&[data-star="1"] {
									background-position: 0 -320px;
								}

								&[data-star="1"] {
									background-position: 0 -360px;
								}

								&[data-star="1.5"] {
									background-position: 0 -340px;
								}

								&[data-star="2"] {
									background-position: 0 -400px;
								}

								&[data-star="2.5"] {
									background-position: 0 -380px;
								}

								&[data-star="3"] {
									background-position: 0 -440px;
								}

								&[data-star="3.5"] {
									background-position: 0 -420px;
								}

								&[data-star="4"] {
									background-position: 0 -480px;
								}

								&[data-star="4.5"] {
									background-position: 0 -460px;
								}

								&[data-star="5"] {
									background-position: 0 -500px;
								}


							}

							span {}
						}

						&__link {
							a {
								font-size: 14px;
								line-height: 20px;
								color: #757280;

								&:hover {
									text-decoration: underline;
								}
							}
						}


					}

					&__adress {
						display: flex;
						flex-direction: column;
						margin-right: auto;

						span {
							color: #757280;
							font-size: 12px;
							line-height: 18px;
						}
					}

				}

				&__requast {
					margin-top: 16px;
					border-radius: 4px;
					background-color: #f4f4f4;
					border-color: #eeeeef;
					padding: 16px;


					&__button {
						margin-bottom: 16px;

						>a {
							font-size: 14px;
							line-height: 22px;
							font-weight: 500;
							color: #2b273c;
							border: 1px solid #bbbac0;
							background-color: #fff;
							padding: 0 18px;
							border-radius: 4px;
							transition: all 0.5s;

							&:hover {
								background-color: #e2e2e6;
							}

						}
					}

					&__text {
						margin-right: auto;


						>p {
							margin-bottom: 0;
							color: #2b273c;
							font-weight: 400;
							font-size: 13px;
							line-height: 20px;

							span {
								color: #2b273c;
								font-weight: 500;
							}
						}
					}
				}
			}
		}

		&__number-page {
			border-bottom: 1px solid #eeeeef;
			border-top: 1px solid #eeeeef;
			padding: 20px 0;
			display: flex;
			align-items: center;

			&__number {
				ul {
					display: flex;
					align-items: center;

					li {
						padding: 0 6px;



						a {
							color: #757280;
							height: 30px;
							font-size: 16px;
							line-height: 22px;
							font-weight: 400;
						}
					}
				}
			}

			&__cunter {
				margin-right: auto;

				span {
					color: #757280;
					height: 30px;
					font-size: 16px;
					line-height: 22px;
					font-weight: 400;
				}
			}

		}

		&__quastion {
			display: flex;
			align-items: center;
			border-radius: 4px;
			border: 1px solid #eeeeef;
			padding: 24px;
			margin-top: 32px;
			margin-bottom: 24px;

			&__img {
				margin-left: 32px;

				img {}
			}

			&__text {
				h3 {
					color: #2b273c;
					font-weight: 500;
					font-size: 20px;
					line-height: 26px;
				}

				p {
					margin-bottom: 0;
					margin-top: 8px;
					color: #757280;
					font-size: 16px;
					line-height: 22px;
				}
			}

			&__button {
				margin-right: auto;


				a {
					color: #fff;
					background: #f43939;
					border: 1px solid #f43939;
					position: relative;
					line-height: 40px;
					border-radius: 4px;
					padding: 0 16px;
					font-weight: 500;
					font-size: 16px;

					&:hover {
						box-shadow: 0 1px 4px 0 rgba(0, 0, 0, .3);
					}
				}
			}
		}

		&__text {
			p {
				font-size: 14px;
				line-height: 20px;
				color: #757280;
				margin-bottom: 32px;

				a {
					font-size: 14px;
					line-height: 20px;
					color: #00838f;

					&:hover {
						text-decoration: underline;
					}
				}
			}
		}

		&__more {
			display: none;
			margin-bottom: 20px;

			a {
				color: #fff;
				background-color: #0097ec;
				border: 1px solid #0097ec;
				border-radius: 4px;
				line-height: 43px;
				display: block;
				text-align: center;
				font-size: 14px;
				font-weight: 500;
			}
		}

		&__information {
			h3 {
				font-size: 20px;
				line-height: 26px;
				color: #2b273c;
				font-weight: 500;
				margin-bottom: 24px;


			}

			&__item {

				ul {
					li {
						margin-top: 8px;

						a {
							font-weight: 400;
							font-size: 14px;
							line-height: 20px;
							color: #757280;

							&:hover {
								text-decoration: underline;
							}
						}
					}
				}


				>a {
					font-size: 14px;
					line-height: 20px;
					color: #757280;
					font-weight: 500;
					margin-top: 16px;
					display: block;

					&:hover {
						text-decoration: underline;
					}
				}
			}
		}

		&__work {
			margin-top: 32px;
			margin-bottom: 32px;


			h3 {
				margin-bottom: 32px;
				font-size: 24px;
				line-height: 30px;
				color: #2b273c;
				font-weight: 500;
			}

			&__list {
				display: flex;
				align-items: center;
				flex-wrap: wrap;
			}

			&__item {
				margin-bottom: 12px;
				margin-left: 12px;
				width: 140px;
				height: 140px;

				img {
					border-radius: 4px;
					width: 100%;
					height: 100%;

				}
			}
		}
	}

	.form-check {
		display: flex;
		align-items: center;
		padding-left: 0;
		padding-top: 4px;
	}

	.form-check-input {
		margin-top: 0;
		margin-left: 10px;
		position: static;
		width: 20px;
		height: 20px;


	}

	.form-check-input:hover {
		&:hover {
			box-shadow: 0 0 15px #969393;
		}
	}

	.form-check-label {
		color: #2b273c;
		font-weight: 400;
		font-size: 14px;
		line-height: 20px;
		margin-left: 8px;
	}


}

