							margin-bottom: 16px;

							>a {
								font-size: 12px;
								line-height: 22px;
								font-weight: 500;
								color: #2b273c;
								border: 1px solid #bbbac0;
								background-color: #fff;
								padding: 0 6px;
								border-radius: 4px;

								&:hover {
									background-color: #e2e2e6;
								}

							}
						}

						&__text {
							margin-right: auto;


							>p {
								margin-bottom: 0;
