.sign-up {
	&__item {
		width: 300px;
		margin: 30px;

		&__title {
			text-align: center;
			margin-bottom: 12px;

			h2 {
				color: #d32323;
				line-height: 28px;
				margin-bottom: 6px;
				font-size: 22px;

			}

			p {
				margin-bottom: 0;
				color: #333;
				font-weight: 500;
			}
		}

		.form-check {
			display: flex;
			margin-bottom: 12px;
			justify-content: center;
			align-items: center;
			padding-left: 0;
			text-align: center;

			label {
                font-size: 10px;
                text-align: right;

				a {
					color: #0073bb;
					padding: 0 5px;
					cursor: pointer;

					&:hover {
						text-decoration: underline;
					}

				}
			}

		}

		.form-row {
			margin-bottom: 9px;
		}

		.form-group {
			margin-bottom: 12px;

			&:nth-child(3) {
				margin-bottom: 24px;
			}
		}

		&__lable {
			display: flex;
			align-items: center;
			margin-bottom: 6px;

			p {
				color: #333;
				font-size: 14px;
				font-weight: 500;
				margin-bottom: 0;
				padding-left: 5px;

			}

			span {
				color: #999;
				font-size: 12px;
			}
		}

		&__birthday {
			margin-bottom: 9px;
		}


		&__login {
			border-bottom: 1px solid #e6e6e6;
			margin-bottom: 9px;

			.btn-primary {

				color: #fff;
				background: #d32323;
				border: 1px solid #d32323;
				width: 300px;
				line-height: 22px;
				margin-bottom: 12px;

				&:hover {
					background-color: #dc4f4f;
				}

				&:focus {

					background-color: #be2020;
					box-shadow: none;
					outline: none;

				}

				&.btn-primary:not(:disabled):not(.disabled).active:focus,
				&.btn-primary:not(:disabled):not(.disabled):active:focus,
				&.show>.btn-primary.dropdown-toggle:focus {
					box-shadow: none
				}


				&:not(:disabled):not(.disabled).active,
				&:not(:disabled):not(.disabled):active {
					color: #fff;
					background-color: #dc4f4f;
					border: 1px solid #d32323;
				}

			}

		}

		&__form {
			display: flex;
			flex-direction: column;
			justify-content: center;
			text-align: center;

			&__facebook {
				margin-bottom: 12px;

				a {
					color: #fff;
					background: #1877f2;
					border: 1px solid #1877f2;
					line-height: 43px;

					display: block;
					text-align: center;
					font-size: 16px;
					font-weight: 500px;
					cursor: pointer;
					border-radius: 3px;

					&:hover {
						background-color: #4692f5;
					}
				}
			}

			&__google {
				margin-bottom: 12px;

				a {
					color: #666;
					background: #fff;
					border: 1px solid #ccc;
					line-height: 43px;

					display: block;
					text-align: center;
					font-size: 16px;
					font-weight: 500px;
					cursor: pointer;
					border-radius: 3px;

				}
			}

			&__apple {
				margin-bottom: 12px;

				a {
					color: #fff;
					background: #000;
					border: 1px solid #000;
					line-height: 43px;

					display: block;
					text-align: center;
					font-size: 16px;
					font-weight: 500px;
					cursor: pointer;
					border-radius: 3px;

					&:hover {
						background-color: #333;
					}

				}
			}
		}

		&__wory {
			text-align: center;
			p {
				margin-bottom: 0;
			}
		}

		&__sub-form {
			margin-top: 12px;
			display: flex;
        
			justify-content: center;
			margin-bottom: 12px;
			p {
				margin-bottom: 0;
				color: #999;
				font-weight: 500;
				margin-left: 5px;
			}

			a {
				color: #0073bb;
				cursor: pointer;
				&:hover {
					text-decoration: underline;
				}

			}
		}

		.form-group {
			// line-height: 30px;
			margin-bottom: 0;

			input {
				color: #495057;
				background-color: #fff;
				border: 1px solid #ced4da;
				border-radius: 3px;
				line-height: 30px;
				margin-bottom: 9px;
				margin-top: 0;
				font-size: 14px;

				&:focus {
					box-shadow: 0 0 4px rgba(0, 115, 187, 0.3);
					border-color: #0073bb;
				}
			}



		}

		.form-row {
			// line-height: 30px;
			margin-bottom: 0;

			input {
				color: #495057;
				background-color: #fff;
				border: 1px solid #ced4da;
				border-radius: 3px;
				line-height: 30px;
				margin-bottom: 9px;
				margin-top: 0;
				font-size: 14px;

				&:focus {
					box-shadow: 0 0 4px rgba(0, 115, 187, 0.3);
					border-color: #0073bb;
				}
			}



		}

		.form-control {
			&:focus {
				color: #495057;
				background-color: #fff;
				border-color: #80bdff;
				outline: 0;
				box-shadow: none;
			}
		}

		.form-check-input {
			position: static;
			margin-top: 0;
			margin-right: 0;
		}

		.form-check-label {
			padding-right: 25px;

		}
	}

	&__image {
		margin-top: 117px;
	}
}

@media(max-width:576px) {
	.sign-up {
		margin-bottom: 36px;

		&__item {
			margin: 30px auto;
		}
	}
}

@media(max-width:480px) {
	.sign-up {
		&__item {
			width: auto;

			.btn-primary {
				width: 100%;
			}
		}
	}
}
