.header-type1
{
    border-bottom: 1px solid #e6e6e6;
    padding: 24px 40px 0;
    
    background-color: #fff;
    &__menu {
        display: flex;
        align-items: center;

        &__nav {
            margin-right: auto;

            ul {
                display: flex;
                align-items: center;

                li {

                    a {
                        line-height: 40px;
                        font-size: 14px;
                        display: block;
                        text-align: center;
                        padding: 0 16px;
                        border-radius: 4px;
                        color: #2b273c;
                        transition: 0.8s;
                        font-weight: 500;

                        &:hover {
                            background-color: #e0e0e0;

                        }
                    }
                    &:nth-child(1)
                    {
                        margin-left: 8px;
                        a
                        {
                            padding: 0 8px;


                        }
                    }
                    &:nth-child(2)
                    {
                        margin-left: 8px;
                        a
                        {
                            padding: 0 8px;
                            

                        }
                    }

                    &:nth-child(3) {
                        a {
                            border: 1px solid #bbbac0;
                            width: 113px;
                            // margin-right: 16px;

                            &:hover {
                                background-color: #e0e0e0;
                            }
                        }
                    }

                    &:nth-child(4) {
                        margin-right: 12px;
                        width: 127px;

                        a {
                            color: #fff;
                            background-color: #f43939;
                            border: 1px solid #f43939;
                            position: relative;

                            &:hover {
                                box-shadow: 0 1px 4px 0 rgba(0, 0, 0, .3);
                            }
                        }
                    }

                    
                }
            }
        }

        &__user-nav
        {
            >ul
            {
                display: flex;
                align-items: center;
                >li
                {
                    margin-left: 8px;
                    position: relative;
                    >a
                    {
                        line-height: 40px;
                        min-width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        &:hover
                        {
                            background-color: #eeeeef;
                        }
                        svg
                        {
                            line-height: inherit;
                        }
                        img
                        {
                            width: 36px;
                            height: 36px;
                            border-radius: 50%;
                        }
                    }
                    &:last-child
                    {
                        margin-left: 0;
                        >a
                        {
                            &:hover
                            {
                                background-color: transparent;
                            }
                        }
                    }
                    >ul
                    {
                        position: absolute;
                        left: 0;
                        top: 100%;
                        z-index: 999;
                        border-radius: 4px;
                        background-color: #fff;
                        box-shadow: 0 0 18px rgba(0,0,0,0.15);
                        width: 258px;
                        padding: 16px;
                        display: none;
                        >li
                        {
                            a
                            {
                                line-height: 40px;
                                display: flex;
                                align-items: center;
                                padding: 0 8px;
                                color: #2b273c;
                                border-radius: 4px;
                                svg
                                {
                                    margin-left: 10px;
                                }
                                &:hover
                                {
                                    background-color: #eeeeef; 
                                }
                            }
                            &.has-seprator
                            {
                                padding-bottom: 8px;
                                margin-bottom: 8px;
                                border-bottom: 1px solid #eeeeef;
                            }
                        }
                    }
                }
            }
        }

        &__search {
            width: calc(100% - 630px);
            background-color: #fff;
            display: flex;
            align-items: center;
            border-radius: 4px;
            box-shadow: 0 2px 18px rgba(0, 0, 0, .15);
            position: relative;

            &__right {
                $self: &;
                background-color: #fff;
                position: relative;
                border-radius: 4px;
                flex: 0 0 calc(50% - 28px );
                max-width: calc(50% - 28px );
                line-height: 48px;


                &--open {
                    border-bottom-right-radius: 0;

                    #{$self}__menu-bottom {
                        display: block;
                    }

                }

                input {
                    border: none;
                    font-size: 16px;
                    height: 22px;
                    color: #333;
                    padding: 0 15px;
                    width: 100%;
                }

                &__menu-bottom {
                    background-color: #fff;
                    box-shadow: 0 2px 18px rgba(0, 0, 0, 0.15);
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 100%;
                    border-bottom-left-radius: 4px;
                    border-bottom-right-radius: 4px;
                    border-top: 1px solid #e6e6e6;
                    display: none;
                    z-index: 2;

                    ul {
                        // padding-right: 10px;
                        z-index: 2;
                        position: relative;

                        li {
                           

                            margin:0px 15px;
                            line-height: 45px;
                            &:first-child
                            {
                                margin-top: 15px;
                            }
                            &:last-child
                            {
                                margin-bottom:15px;
                            }
                            &:hover
                            {
                                background-color: #e0e0e0;
                                border-radius: 4px;

                            }
                            a {
                                color: #2b273c;
                                svg
                                {
                                    margin:0 6px ;
                                }
                            }
                        }
                    }
                }
            }

            &__left {
                flex: 0 0 calc(50% - 28px);
                max-width:calc(50% - 28px);
                line-height: 48px;
                background-color: #fff;
                border-radius: 4px;
                $self: &;
                position: relative;
                &--open {
                    border-bottom-right-radius: 0;
                    #{$self}__menu-bottom {
                        display: block;
                    }
                }
                input {
                    border: none;
                    font-size: 16px;
                    height: 22px;
                    padding: 0 10px;
                    width: 100%;
                    color: #333;
                    border-right: 1px solid #e6e6e6;
                }

                &__menu-bottom {
                    background-color: #fff;
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 100%;
                    border-bottom-left-radius: 4px;
                    border-bottom-right-radius: 4px;
                    border-top: 1px solid #e6e6e6;
                    display: none;
                    z-index: 2;

                    ul {
                        // padding-right: 10px;
                        z-index: 2;
                        position: relative;

                        li {
                            margin:0px 15px;
                            line-height: 45px;
                            &:first-child
                            {
                                margin-top: 15px;
                            }
                            &:last-child
                            {
                                margin-bottom:15px;
                            }
                            &:hover
                            {
                                background-color: #e0e0e0;
                                border-radius: 4px;

                            }
                            a {
                                color: #2b273c;
                                svg
                                {
                                    margin:0 6px ;
                                }
                            }
                        }
                    }
                }
            }

            &__button {
                flex: 0 0 56px;
                max-width: 56px;
                background-color: #d32323;
                border-bottom-left-radius: 4px;
                border-top-left-radius: 4px;
                line-height: 48px;
                position: absolute;
                left: 0;
                bottom: 0;
                top: 0;
                width: 56px;


                button {
                    padding: 0;
                    border: none;
                    width: 100%;
                    background-color: rgb(244, 57, 57);
                    border-bottom-left-radius: 4px;
                    border-top-left-radius: 4px;

                    svg {
                        color: red;
                        font-size: 24px;
                        fill: #fff;
                    }
                }
            }

        }


        &__logo {

            display: flex;
            align-items: center;
            margin-left: 32px;
            flex: 0 0 80px;
            max-width: 80px;


            a {
                display: block;

                img {
                    width: 80px;
                    height: 40px;

                }
            }

        }
    }

    &__menu-bottom {
        text-align: center;
        display: flex;
        margin-right: 112px;

        >ul {
            display: flex;
            align-items: center;



            >li {
                margin: 0 16px;
                position: relative;
                border-bottom: 4px solid transparent;
              

                &:hover {
                    border-bottom: 4px solid #e83636;

                }

                >a {
                    color: #2b273c;
                    line-height: 60px;
                    font-size: 14px;
                    display: flex;
                    align-items: center;
                    order:1;

                    svg {
                        margin-right: 10px;
                        order:2;
                    }
                }

                ul {
                    columns: 2;
                    visibility: hidden;
                    opacity: 0;
                    background-color: #fff;
                    position: absolute;
                    right: 0;
                    top: 100%;
                    margin-top: 4px;
                    transition: 0.5s;
                    z-index: 2;
                    border-radius: 3px;
                    border: 1px solid #e6e6e6;
                    box-shadow: 0 2px 8px rgba(0, 0, 0, .2);
                    width: 350px;
                    padding: 15px;


                    li {
                        line-height: 40px;
                        text-align: right;
                        

                        &:hover {
                            background-color: #e0e0e0;
                            border-radius: 4px;
                        }

                        a {
                            color: #2b273c;

                            svg {
                                margin:0 6px ;
                            }


                        }
                    }
                }

                &:hover {
                    >ul {
                        opacity: 1;
                        visibility: visible
                    }
                }


            }
        }
    }
}

@media(max-width:1140px)
{
    .header-type1
    {
        &__menu
        {
            &__search
            {
                width: calc(100% - 400px);
            }
            &__nav
            {
                ul
                {
                    li
                    {
                        &:nth-child(1)
                        {
                            display: none;
                        }
                        &:nth-child(2)
                        {
                            display: none;
                        }
                    }
                }
            }
            &__user-nav
            {
                > ul
                {
                    > li
                    {
                        &:not(.has-sub)
                        {
                            display: none;
                        }
                    }
                }
            }
        }
    }
}




@media(max-width: 975px) {
    .header-type1 {
       
        &__menu
        {
            &__search
            {
                width: calc(100% - 260px);
            }
            &__nav
            {
                ul
                {
                    li
                    {
                        &:last-child
                        {
                            display: none;
                        }
                    }
                }
            }
        }
     
    }
}

@media(max-width:760px)
{
    .header-type1
    {
        &__menu-bottom
        {
            ul
            {
                li
                {
                    &:nth-child(3)
                    {
                        display: none;
                    }
                }
            }
        }
    }
}
@media(max-width:749px)
{
    .header-type1
    {
        display:none;
    }
}