.events {
	margin-bottom: 63px;
	height: 400px;
	border-bottom: 1px solid #e6e6e6;
	background: #f5f5f5;

	&__nav {
		padding-top: 12px;
		display: flex;
		align-items: center;
		padding-bottom: 11px;
		border-bottom: 1px solid #e6e6e6;

		&__right {
			h4 {
				color: #333;
				font-size: 16px;

			}

		}

		&__left {
			margin-right: auto;

			ul {
				display: flex;
				align-items: center;

				li {
					margin-right: 12px;

					&:last-child {
						border-right: 1px solid #e6e6e6;
						padding-right: 6px;

						margin-right: 6px;

					}

					a {
						color: #0073bb;
						display: flex;
						align-items: center;

						svg {
							fill: #0073bb;
						}

						&:hover {
							text-decoration: underline;

						}
					}
				}

			}

		}
	}

	&__spotlight {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 24px 0;

		&__title {
			h2 {
				color: #d32323;
				line-height: 26px;
				font-size: 21px;
				font-weight: 500;
			}
		}

		&__button {

			a {
				color: #fff;
				line-height: 36px;
				transition: all 0.6s;
				background: #d32323;
				border: 1px solid #d32323;
				border-radius: 3px;
				display: flex;
				font-size: 14px;
				padding: 0 19px;
				font-weight: 500;

				&:hover {
					background-color: #dc4f4f;
					border: 1px solid #dc4f4f;


				}

			}
		}
	}

	&__content {
		&__item {
			padding-bottom: 10px;
			padding: 10px 11px 15px;

			background: #fff;
			border: 1px solid #e6e6e6;
			border-radius: 4px;
			padding-bottom: 10px;


			&--right {
				padding: 0;
				display: flex;
				padding-bottom: 0;

			}

			&__title {
				h3 {
					color: #d32323;
					margin-bottom: 11px;
					font-size: 16px;
					font-weight: 500;
					line-height: 21px;
				}
			}

			&__nav {
				ul {
					li {
						margin-bottom: 8px;

						&:last-child {
							margin-bottom: 0;
						}

						a {
							color: #0073bb;

							&:hover {
								text-decoration: underline;
							}

							svg {
								fill: #0073bb;
								margin-left: 12px;

							}
						}
					}
				}
			}

			&__image {
				flex: 0 0 300px;
				max-width: 300px;

				img {
					border-radius: 0 4px 4px 0;
					width: 300px;
					height: 300px;

				}
			}

			&__text {

				flex: 0 0 calc(100% - 300px);
				max-width: calc(100% - 300px);

				&__top {
					padding: 18px;
					height: 260px;
					overflow: hidden;
					border-bottom: 1px solid #e6e6e6;
					margin-bottom: 12px;

					&__title {
						margin-bottom: 6px;

						a {
							color: #0073bb;
							font-size: 19px;
							font-weight: 500;

							&:hover {
								text-decoration: underline;
							}
						}
					}

					&__date {
						margin-bottom: 6px;


						svg {
							fill: #666;
						}

						span {
							color: #333;
						}
					}

					&__location {
						margin-bottom: 6px;

						svg {
							fill: #666;
						}

						span {
							color: #333;
						}
					}

					&__desc {
						position: relative;


						p {
							text-align: justify;
							color: #333;
							margin-bottom: 0;
							line-height: 20px;

						}

						&:before {
							position: absolute;
							content: "";
							bottom: 10px;
							top: 100px;
							left: 0;
							right: 0;
							background: linear-gradient(to bottom, rgba(255, 255, 255, 0), #fff);

						}
					}
				}

				&__bottom {
					padding: 0 18px;
					display: flex;
					align-items: center;
					justify-content: space-between;

					a {
						color: #0073bb;

						&:hover {
							text-decoration: underline;
						}

					}

					span {
						color: #999;

					}
				}
			}
		}
	}
}

@media(max-width:992px) {
	.events {
		&__content {
			&__item {
				&__image {
					flex: 0 0 200px;
					max-width: 200px;

					img {
						height: 100%;
					}
				}

				&__text {
					flex: 0 0 calc(100% - 200px);
					max-width: calc(100% - 200px);

					&__top {
						height: 270px;
					}
				}
			}
		}
	}
}

@media(max-width:1020px) {
	.events {
		&__nav {
			display: none;
		}
	}
}


@media(max-width:767px) {
	.events {
		height: auto;

		&__content {
			&__item {
				margin-bottom: 20px;

				&__image {
					flex: 0 0 250px;
					max-width: 250px;

					img {
						height: 100%;
					}
				}

				&__text {
					flex: 0 0 calc(100% - 250px);
					max-width: calc(100% - 250px);

					&__top {

						&__title {
							a {
								font-size: 16px;
							}
						}
					}
				}
			}
		}
	}
}

@media(max-width:576px) {
	.events {
		&__nav {
			&__left {
				ul {
					li {
						margin-right: 8px;

						a {
							font-size: 13px;
						}
					}
				}
			}
		}

		&__content {
			&__item {
				margin-bottom: 20px;

				&__image {
					flex: 0 0 200px;
					max-width: 200px;

					img {
						height: 100%;
					}
				}

				&__text {
					flex: 0 0 calc(100% - 200px);
					max-width: calc(100% - 200px);

					&__top {

						&__title {
							a {
								font-size: 14px;
							}
						}
					}
				}
			}
		}

	}
}

@media(max-width:599px) {
	.events {
		&__spotlight {
			&__title {
				display: none;
			}
		}
	}
}

@media(max-width:480px) {
	.events {
		margin-bottom: 20px;

		&__nav {
			display: none;
		}

		&__spotlight {
			&__title {
				display: none;
			}

			&__button {
				width: 100%;

				a {
					justify-content: center;
				}
			}
		}

		&__content {
			&__item {
				display: block;
				margin-bottom: 20px;

				&__image {
					flex: 0 0 100%;
					max-width: 100%;

					img {
						height: 300px;
						width: 100%;
					}
				}

				&__text {
					flex: 0 0 100%;
					max-width: 100%;

					&__top {

						&__title {
							a {
								font-size: 14px;
							}
						}

					}
				}
			}
		}


	}
}
