.header-mobile1 {
	display: none;
	padding-top: 16px;
	border-bottom: 1px solid #eeeeef;

	&__menu-mobail {

		&__top {
			display: flex;
			align-items: center;
			padding-bottom: 10px;

			&__log-in {

				margin-left: 16px;

				a {
					color: #2b273c;
					background: transparent;
					border: 1px solid #bbbac0;
					padding: 0 16px;
					border-radius: 4px;
					display: flex;
					line-height: 38px;
					transition: all .5s;

					&:hover {
						background-color: #e0e0e0;
					}
				}


			}

			&__icon-search {
				margin-right: auto;

				svg {
					fill: rgba(43, 39, 60, 1);
				}
			}

			&__user-nav {
                margin-right: 15px;
				>ul {
					display: flex;
					align-items: center;

					>li {
						position: relative;

						>a {
							line-height: 40px;
							min-width: 40px;
							height: 40px;
							border-radius: 50%;
							display: flex;
							align-items: center;
							justify-content: center;

							img {
								width: 36px;
								height: 36px;
								border-radius: 50%;
							}
						}

						>ul {
							position: absolute;
							left: 0;
							top: 100%;
							z-index: 999;
							border-radius: 4px;
							background-color: #fff;
							box-shadow: 0 0 18px rgba(0, 0, 0, 0.15);
							width: 258px;
							padding: 16px;
							display: none;

							>li {
								a {
									line-height: 40px;
									display: flex;
									align-items: center;
									padding: 0 8px;
									color: #2b273c;
									border-radius: 4px;

									svg {
										margin-left: 10px;
									}

									&:hover {
										background-color: #eeeeef;
									}
								}

								&.has-seprator {
									padding-bottom: 8px;
									margin-bottom: 8px;
									border-bottom: 1px solid #eeeeef;
								}
							}
						}
					}
				}
			}

			&__logo {


				img {}

			}

		}

		&__bottom {

			>ul {
				display: flex;
				width: 100%;
				align-items: center;
				position: relative;


				>li {

					padding-bottom: 10px;
					position: relative;
					border-bottom: 4px solid transparent;

					&:hover {
						border-bottom: 4px solid #e83636;

					}

					&:first-child {
						border-right: none;
					}

					&:last-child {
						border-left: none;
					}

					>a {
						display: flex;
						align-items: center;
						padding: 0 12px;
						font-size: 12px;
						color: black;
						line-height: 40px;
						order: 1;

						&:hover {
							color: #0073bb;
							fill: #0073bb;
						}



						svg {
							margin-right: 5px;
							order: 2;
						}
					}

					>ul {
						visibility: hidden;
						opacity: 0;
						background-color: #fff;
						position: absolute;
						right: 0;
						width: 200px;
						// padding: 16px;
						top: 54px;
						z-index: 2;
						max-height: 300px;
						overflow-y: auto;
						box-shadow: 0 2px 8px rgba(0, 0, 0, .2);


						>li {
							margin: 6px 12px;

							&:hover {
								a {
									background-color: #e0e0e0;
								}
							}

							>a {
								font-size: 12px;
								color: #2b273c;
                                line-height: 40px;
                                border-radius: 4px;
                                padding: 0 8px;
                                display: flex;
                                align-items: center;
								svg {
									fill: #2b273c;
									margin-left: 6px;
								}
							}
						}

					}


					&:hover {
						>ul {
							opacity: 1;
							visibility: visible;
						}
					}

				}
			}
		}

	}
}

@media(max-width:749px) {
	.header-mobile1 {
		display: block;

	}
}

@media(max-width:610px) {
	.header-mobile1 {

		&__menu-mobail {
			&__bottom {
				Ul {
					li {
						&:nth-child(3) {
							display: none;
						}
					}
				}
			}
		}
	}
}

@media(max-width: 480px) {
	.header-mobile1 {


		&__menu-mobail {
			&__bottom {
				Ul {
					li {
						a {
							padding: 0 6px;
						}

						&:nth-child(2) {
							display: none;
						}
					}
				}
			}

		}
	}
}
