.footer-type4{
    padding-top: 50px;
    font-size: 14px;
    line-height: 1.28571em;
    background-color: #f5f5f5;
    padding-bottom:36px;

    .container-fluid {
        padding: 0;
    }

    &__item {
        select {
                    
        }

        option {
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
        }
        .dropdown-menu
        {
           width: auto;
           border-radius:0;
           box-shadow: 0 2px 8px rgba(0,0,0,0.2);
        }
        .form-control
        {
            background-color:transparent;
        }

        .bootstrap-select:not(.input-group-btn),
        .bootstrap-select[class*=col-] {
            display: flex;
            align-items: center;

        }


        .btn {
            padding: 0;
        }

        .bootstrap-select .dropdown-toggle .filter-option {
            padding-left: 10px;
        }


        .bootstrap-select .dropdown-menu {
           
            ul
            {
                li
                {
                    line-height:16px;
                    margin-bottom: 0;
                }
            }
        }

        .bootstrap-select>.dropdown-toggle {
            background-color: transparent;
            border: transparent;
            width: auto;
            margin-bottom: 24px;
        }

        .bootstrap-select>.dropdown-toggle:hover .filter-option-inner-inner {
            text-decoration: underline;
            text-align:right;

        }

        .btn-light:not(:disabled):not(.disabled).active,
        .btn-light:not(:disabled):not(.disabled):active,
        .show>.btn-light.dropdown-toggle {
            color: #757280;
            background-color: transparent;
            border-color: transparent;
            border: none;
            outline: none;
            box-shadow: none;

        }
        .btn-light
        {
            color: #757280;
        }

        .bootstrap-select .dropdown-toggle:focus,
        .bootstrap-select>select.mobile-device:focus+.dropdown-toggle {
            outline: none !important;
            box-shadow: none;
        }

        .filter-option-inner-inner {
            text-align: right;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
        }

        .dropdown-menu {
            .inner {
                .show {
                    text-align: right;
                }
            }

            .selected {
                &.active {
                    background-color:  #757280;;
                }
            }

        }
        
        &__title {
            margin-bottom: 16px;
            margin-top: 24px;

            h2 {
                font-weight: 500;
                margin-bottom: 6px;
                font-size: 16px;
                line-height: 22px;
                color: #2b273c;
            }
        }

        ul {


            li {

                margin-bottom: 6px;

                a {
                    font-size: 14px;
                    line-height: 20px;
                    color: #757280;
                }
            }
        }
    }


    &__copyright {
    
        padding-top: 32px;

        span {
            color: #757280;
            font-size: 12px;
            line-height: 18px;
        }
    }

}

@media(max-width:767px)
{
    .footer-type4
    {
        display: none;
    }
}