.upload-pic
{
    padding: 30px 0 80px;

    .dropzone
    {
        border: 3px dashed #999;
        border-radius: 8px;
        padding: 50px 15px;
        margin: 0 15px;
        .dz-message
        {
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;
            >span
            {
                font-weight: 600;
                font-size: 28px;
                color: #333;
                margin-bottom: 15px;
            }
            .hr-line
            {
                position: relative;
                margin-bottom: 15px;
                font-size: 12px;
                display: flex;
                align-items: center;
                justify-content: center;
                width: 700px;
                max-width: 100%;
                span
                {
                    position: relative;
                    z-index: 2;
                    padding: 0 15px;
                    background-color: #fff;
                    font-size: 14px;
                }
                &:before
                {
                    content: '';
                    position: absolute;
                    width: 500px;
                    max-width: 90%;
                    top: 50%;
                    height: 1px;
                    background-color: #ccc;
                    z-index: 1;
                }
            }
            button
            {
                color: #fff;
                background: #d32323;
                padding: 0 35px;
                line-height: 36px;
                border: none;
                border-radius: 3px;
                font-size: 13px;
                font-weight: 600;
                display: inline-block;
            }
        }
    }
}