.services {
    display: none;
    padding: 12px;
    border-top: 1px solid #ccc;

    &__title {
        padding-bottom: 12px;

        h3 {
            font-size: 16px;
            line-height: 17px;
        }
    }

    &__slider {
        margin-bottom: 12px;

        a {

            display: flex;
            align-items: center;
            flex-direction: column;

            img {
                margin-bottom: 6px;
                border-radius: 4px;
            }

            span {
                font-size: 14px;
                line-height: 18px;
                color: #333;
            }
        }

        .swiper-button-prev
        {
            height: 30px;
            width: 30px;
            border-radius: 50%;
            background: #fff;
            box-shadow: 0 2px 6px rgba(0,0,0,.15);
            text-align: center;
            cursor: pointer;
            right:0px;
            top: 50%;
            color: rgb(102, 102, 102);
            &:after
            {
                fill: rgb(102, 102, 102);
                font-size: 20px;

            }
           
           
        }



    }
}

@media(max-width: 480px) {
    .services {
        display: block;

    }
}


@media(max-width:576px) {
    .services {
        display: block;

    }
}