.main-footer {
    border-top: 1px solid #ccc;
    // padding: 36px 0 18px;
    padding-top: 36px;

    font-size: 14px;
    line-height: 1.28571em;
    background-color: #f5f5f5;
    // position: relative;

    &__item {

        &__title {
            margin-bottom: 12px;

            h2 {
                font-weight: 500;
                margin-bottom: 6px;
                font-size: 16px;
                line-height: 21px;
                color: #d32323;
            }
        }

        ul {
            margin-bottom: 24px;



            li {

                margin-bottom: 6px;

                a {
                    color: #0073bb;

                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }

        &__language {
            margin-bottom: 24px;

            h2 {
                color: #d32323;
                font-weight: 500;
                margin-bottom: 6px;
                font-size: 16px;
                line-height: 21px;
                color: #d32323;
                margin-bottom: 12px;
            }
        }

        &__Countries {
            margin-bottom: 24px;

            h2 {
                color: #d32323;
                font-weight: 500;
                margin-bottom: 6px;
                font-size: 16px;
                line-height: 21px;
                color: #d32323;
                margin-bottom: 12px;
            }
        }
    }

    &__image {

        margin-bottom: 18px;
        margin-top: 42px;
        text-align: center;

        // position: absolute;
        // right: 25%;
        // bottom: 0;
        // z-index: 1;
    }

    &__copyright {
        padding-bottom: 18px;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;


        span {
            line-height: 14px;
            font-size: 12px;
        }

        img {
            &:first-of-type {

                width: 38px;
                height: 19px;

            }

            width: 13px;
            height: 19px;
            margin: 0 4px;

        }
    }
}

