.item-location {
	position: relative;


	.container-item-location {
		max-width: 1144px;
		margin: 0 auto;
		border-bottom: 1px solid #eeeeef;
		padding-bottom: 32px;
	}

	&__item {
		position: sticky;
		top: 15px;

		&__account {
			border-radius: 4px;
			border: 1px solid #eeeeef;
			margin-top: 24px;

			ul {
				display: flex;
				flex-direction: column;
				padding: 24px;




				li {
					border-bottom: 1px solid #eeeeef;
					padding: 16px 0px;

					&:nth-child(2) {
						a {
							color: #2b273c;
							font-weight: 400;
							font-size: 14px;
							line-height: 20px;
						}
					}


					&:last-child {
						border-bottom: none;
						padding-bottom: 0px;
					}

					&:first-child {
						padding-top: 0px;
					}

					a {

						align-items: center;
						display: flex;
						color: #00838f;
						font-weight: 400;
						font-size: 14px;
						line-height: 20px;


						&:hover {
							text-decoration: underline;
						}


						>i {
							padding-left: 15px;

						}
					}

					i {
						padding-left: 15px;


					}
				}
			}
		}

		&__alert {
			border-radius: 4px;
			border: 1px solid #eeeeef;
			margin-top: 24px;
			padding: 24px;
			text-align: center;



			&__icon {
				text-align: right;


				i {
					font-size: 24px;
					line-height: 36px;
					color: #8e8c97;
				}


			}

			&__image {
				padding-bottom: 24px;
			}

			&__imagee {
				padding-bottom: 24px;
				display: none;
			}

			&__title {
				margin-bottom: 6px;

				h5 {
					font-size: 16px;
					line-height: 22px;

				}




				&__text {
					p {
						color: #2b273c;
						font-weight: 400;
						font-size: 14px;
						line-height: 20px;
					}
				}


			}

			&__button {
				padding-bottom: 24px;


				a {
					border-radius: 4px;
					border: 1px solid #bbbac0;
					font-size: 16px;
					line-height: 40px;
					display: block;
					font-weight: 500;
					color: #2384c0;
					transition: 0.8s;

					&:hover {
						background-color: #e0e0e0;

					}
				}

			}

		}

	}

	&__content {
		padding-top: 24px;


		&__location {
			border-bottom: 1px solid #eeeeef;

			&__title {


				h1 {
					font-size: 38px;
					line-height: 54px;
					color: #2b273c;
					font-weight: 500;



					a {
						color: #00838f;
						font-size: 14px;
						line-height: 19px;
						display: inline-flex;
						align-items: center;
						margin-right: 6px;

						svg {
							font-size: 18px;
							margin-left: 5px;


						}
					}
				}

			}

			&__icon {
				display: flex;
				align-items: center;
				margin: 16px 0;

				&__icon-star {
					width: 176px;
					height: 32px;
					background-image: url(../img/star.png);
					background-position: 0 0;
					background-repeat: no-repeat;

					&[data-star="1"] {
						background-position: 0 -64px;
					}

					&[data-star="1.5"] {
						background-position: 0 -32px;
					}

					&[data-star="2"] {
						background-position: 0 -130px;
					}

					&[data-star="2.5"] {
						background-position: 0 -98px;
					}

					&[data-star="3"] {
						background-position: 0 -192px;
					}

					&[data-star="3.5"] {
						background-position: 0 -160px;
					}

					&[data-star="4"] {
						background-position: 0 -256px;
					}

					&[data-star="4.5"] {
						background-position: 0 -224px;
					}

					&[data-star="5"] {
						background-position: 0 -288px;
					}

				}

				&__text {
					span {
						color: #757280;
						font-size: 16px;
						line-height: 22px;
						margin-right: 6px;
					}
				}

				&__button {
					a {
						font-size: 12px;
						line-height: 24px;
						display: block;
						background-color: #e2e2e6;
						border: 1px solid #eeeeef;
						color: #2b273c;
						padding: 0 8px;
						border-radius: 4px;
						margin-right: 6px;
					}
				}
			}

			&__text {


				a {
					font-size: 16px;
					line-height: 22px;
					color: #757280;
					margin-left: 5px;

					&:last-child {
						border: 1px solid #ccc;
						border-radius: 3px;
						font-size: 12px;
						line-height: 18px;
						color: #2b273c;

						padding: 0 6px;

						&:hover {
							text-decoration: none;
							background-color: #E6E6E6;
						}
					}

					svg {
						margin-left: 6px;
					}

					&:hover {
						text-decoration: underline;
					}

				}
			}

			&__clock {
				margin-top: 16px;
				margin-bottom: 24px;

				p {
					color: #757280;
					font-size: 16px;
					line-height: 22px;
					font-weight: 500;


					span {
						color: #f43939;
						margin-right: 8px;
					}
				}
			}

			&__button {
				margin-bottom: 32px;

				ul {
					display: flex;
					align-items: center;

					li {
						margin-left: 8px;

						&:first-child {
							a {
								color: #fff;
								background-color: #f43939;
								border: 1px solid #f43939;

								&:hover {
									box-shadow: 0 1px 4px 0 rgba(0, 0, 0, .3);
									background-color: #f43939;
									color: #fff;

								}

								svg {
									fill: #fff;
								}

							}
						}

						a {
							display: flex;
							align-items: center;
							line-height: 40px;
							color: #2b273c;
							border: 1px solid #bbbac0;
							transition: 0.8s;
							font-weight: 500;
							text-align: center;
							padding: 0 16px;
							border-radius: 4px;

							&:hover {

								background-color: #e0e0e0;
							}

							svg {
								margin-left: 8px;
								font-size: 24px;


							}
						}
					}
				}
			}
		}

		&__information {
			display: none;
			margin: 10px 0;

			ul {
				display: flex;
				align-items: center;

				li {
					margin: 0 23px;

					a {
						display: flex;
						align-items: center;
						flex-direction: column;
						font-size: 13px;
						color: #666;

						svg {
							fill: rgba(153, 153, 153, 1);
							margin-bottom: 12px;
						}
					}
				}
			}
		}

		&__fotos {
			margin-top: 32px;
			padding-bottom: 32px;
			border-bottom: 1px solid #eeeeef;

			&__title {
				h4 {
					font-size: 20px;
					line-height: 26px;
					color: #2b273c;
					font-weight: 500;
				}

			}

			&__image {
				display: flex;
				align-items: center;
				margin-top: 24px;


				&__img {

					margin-left: 8px;

					img {
						width: 171.75px;
						height: 171.75px;
						border-radius: 4px;
					}
				}

				&__add-img {

					display: flex;



					a {
						width: 171.75px;
						height: 171.75px;
						font-size: 14px;
						line-height: 20px;
						font-weight: 500;
						color: #757280;
						display: flex;
						align-items: center;
						flex-direction: column;
						text-align: center;
						margin: auto;
						border: 2px dashed #ccc;
						border-radius: 4px;
						background-color: #f5f5f5;
						justify-content: center;

						&:hover {
							background-color: #e6e6e6;
						}

						svg {
							text-align: center;
							margin-bottom: 10px;
						}

					}
				}
			}
		}

		&__map {
			margin-top: 32px;
			border-bottom: 1px solid #eeeeef;
			padding-bottom: 32px;


			&__title {
				margin-bottom: 24px;

				h4 {
					font-size: 20px;
					line-height: 26px;
					color: #2b273c;
					font-weight: 500;
				}
			}

			&__content {
				display: flex;

				&__right {
					border-radius: 4px;
					border: 1px solid #e6e6e6;
					overflow: hidden;
					width: 317px;
					height: 244px;

					&__map-location {
						margin-bottom: 16px;
						width: 317px;
						height: 150px;

					}

					&__text {
						padding: 0 16px;
						display: flex;
						align-items: center;

						&__right {
							display: flex;
							flex-direction: column;

							p {
								margin-bottom: 0;
								color: #2b273c;
								font-weight: 500;
								font-size: 14px;
								line-height: 20px;

							}

							span {
								font-weight: 400;
								font-size: 14px;
								line-height: 20px;
								color: #2b273c;
							}
						}

						&__left {
							margin-right: auto;

							a {
								height: 24px;
								display: block;
								color: #2b273c;
								border: 1px solid #bbbac0;
								border-radius: 4px;
								padding: 0 8px;
								transition: 0.8s;
								font-size: 12px;
								line-height: 20px;

								&:hover {

									background-color: #e0e0e0;
								}
							}

						}
					}




				}

				&__left {
					padding-right: 32px;

					ul {

						li {
							display: flex;
							align-items: center;


							span {
								color: #2b273c;
								font-weight: 500;
								font-size: 16px;
								line-height: 30px;
								margin-left: 16px;
							}

							p {
								margin-bottom: 0;
								color: #f43939;
								font-size: 16px;
								line-height: 22px;
								font-weight: 500;
							}


						}
					}

					a {
						display: flex;
						align-items: center;
						color: #00838f;
						font-weight: 400;
						font-size: 14px;
						line-height: 30px;

						svg {
							margin-left: 8px;
						}
					}
				}

			}
		}

		&__desc {
			margin-top: 32px;
			border-bottom: 1px solid #eeeeef;
			padding-bottom: 24px;



			&__title {
				margin-bottom: 24px;

				h4 {}
			}

			&__alert {
				border: 1px solid #eeeeef;
				padding: 16px;
				background-color: #f4f4f4;
				display: flex;
				align-items: center;

				svg {
					margin-left: 16px;
					display: flex;
				}

				p {
					margin-bottom: 0;
					align-items: center;
					color: #2b273c;
					font-size: 12px;
					line-height: 18px;
					// line-height: 35px;
					width: 557px;

					span {
						color: #2b273c;
						font-size: 12px;
						line-height: 18px;
						font-weight: 500;
					}

					a {
						color: #00838f;
						display: block;

						&:hover {
							text-decoration: underline;
							color: #00838f;

						}
					}
				}

				&__close {
					margin-right: auto;

					svg {}

				}


			}

			&__search {

				display: flex;
				align-items: center;
				background-color: #fff;
				margin-top: 16px;
				margin-bottom: 24px;

				&__input {
					width: 388px;
					height: 48px;
					display: flex;
					align-items: center;


					input {
						// border: none;
						line-height: 48px;
						border-radius: 4px;
						padding: 0 16px;
						border-radius: 0 4px 4px 0;
						border: 1px solid #585252;
						line-height: 46px;
						border-left: none;
					}

					button {
						background: #f43939;
						border: 1px solid #f11414;
						width: 56px;
						line-height: 44px;
						border-radius: 4px 0 0 4px;

						svg {
							fill: #fff;
						}
					}
				}

				&__sort {
					display: flex;
					align-items: center;
					margin-right: auto;

					span {
						color: #333;
						margin-left: 4px;
						width: auto;
						margin-left: 6px;

					}

					select {}

					option {
						font-weight: 400;
						font-size: 14px;
						line-height: 20px;
						color: #00838f;
					}

					.bootstrap-select:not(.input-group-btn),
					.bootstrap-select[class*=col-] {
						display: flex;
						align-items: center;

					}

					.bootstrap-select.form-control:not([class*=col-]) {
						width: unset;

					}


					.btn {
						padding: 0;
					}

					.bootstrap-select .dropdown-toggle .filter-option {
						padding-left: 10px;
					}


					.bootstrap-select .dropdown-menu {
						box-shadow: 0 0 18px rgba(0, 0, 0, .15);
					}

					.bootstrap-select>.dropdown-toggle {
						background-color: transparent;
						border: transparent;

					}

					.bootstrap-select>.dropdown-toggle:hover .filter-option-inner-inner {
						text-decoration: underline;

					}

					.btn-light:not(:disabled):not(.disabled).active,
					.btn-light:not(:disabled):not(.disabled):active,
					.show>.btn-light.dropdown-toggle {
						color: #2b273c;
						background-color: transparent;
						border-color: transparent;
						border: none;
						outline: none;
						box-shadow: none;

					}

					.bootstrap-select .dropdown-toggle:focus,
					.bootstrap-select>select.mobile-device:focus+.dropdown-toggle {
						outline: none !important;
						box-shadow: none;
					}

					.filter-option-inner-inner {
						text-align: right;
						font-weight: 400;
						font-size: 14px;
						line-height: 20px;
					}

					.dropdown-menu {
						.inner {
							.show {
								text-align: right;
							}
						}

						.selected {
							&.active {
								background-color: #e6f7fa;
							}
						}

					}

				}
			}

			&__img {}

			&__score {
				background-color: #f4f4f4;
				// width: 466px;
				border: 1px solid #eeeeef;
				border-radius: 4px;
				padding: 24px;

				&__icon {
					display: flex;
					align-items: center;
					justify-content: center;
					border-bottom: 1px solid #eeeeef;

					input {
						display: none
					}

					label {
						width: 32px;
						height: 32px;
						background-position: 0 0;
						background-image: url(../img/star.png);
						background-repeat: no-repeat;
						margin-left: 2px;
						cursor: pointer;


						&[data-star="1"] {
							&:hover {
								background-position: 0 -64px;

								~label {
									background-position: 0 -64px;
								}
							}
						}

						&[data-star="2"] {
							&:hover {
								background-position: 0 -128px;

								~label {
									background-position: 0 -128px;
								}
							}
						}

						&[data-star="3"] {
							&:hover {
								background-position: 0 -192px;

								~label {
									background-position: 0 -192px;
								}
							}
						}

						&[data-star="4"] {
							&:hover {
								background-position: 0 -256px;

								~label {
									background-position: 0 -256px;
								}
							}
						}

						&[data-star="5"] {
							&:hover {
								background-position: 0 -288px;

								~label {
									background-position: 0 -288px;
								}
							}
						}
					}
				}

				&__link {
					text-align: center;
					padding-top: 12px;

					a {
						color: #00838f;
						font-size: 14px;
						line-height: 20px;
						display: inline-block;

						&:hover {
							text-decoration: underline;
						}
					}
				}
			}

		}


