						}

						&[data-star="5"] {
							background-position: 0 -500px;
						}


					}
				}

				&__photo {
					margin-top: 8px;
					margin-bottom: 16px;

					a {
						color: #00838f;

						svg {
							margin-left: 8px;

						}
