				padding-top: 0px;
				margin-bottom: 10px;
				border-bottom: none;

				&__account {
					display: flex;

					&__img {}

					&__text {
						display: flex;
						flex-direction: column;
						margin-right: 8px;

						span {
							display: flex;
							align-items: center;

							svg {
								margin-left: 4px;
							}

							h5 {
								display: inline-block;
								margin-left: 4px;
								font-size: 12px;
							}
						}
					}


				}

				&__link {
					margin-top: 24px;
					display: none;


					&__sher {
						a {
							display: block;
							color: #00838f;
							font-size: 12px;
							line-height: 40px;
							font-weight: 500;

							&:hover {
								text-decoration: underline;
							}

							svg {
								margin-left: 12px;
							}

							span {

								padding: 10px;
							}
						}

						&:first-child {
							>a {
								>span {
									border-bottom: 1px solid #eeeeef;
								}
							}
						}
					}
				}

				&__text {

					&__icon {
						display: flex;
						align-items: center;
						margin-bottom: 12px;
					}

					&__photo {
						margin-top: 8px;
						margin-bottom: 16px;
						display: none;

						a {
							color: #00838f;

							svg {
								margin-left: 8px;

							}
						}

					}

					&__comment {



						p {
							margin-bottom: 0;
							color: #2b273c;
							font-weight: 400;
							font-size: 14px;
							line-height: 20px;
							text-align: justify;
						}
					}

					&__img {
						margin-top: 16px;
						margin-bottom: 16px;

						img {
							height: 348px;
							width: 348px;
							border-radius: 4px;

						}

					}

					&__button {
						margin-bottom: 24px;

						ul {
							display: flex;

							li {
								margin-left: 12px;

								a {
									line-height: 18px;
									color: #2b273c;
									border: 1px solid #bbbac0;
									padding: 0 12px;
									border-radius: 4px;
									transition: 0.8s;

									&:hover {
										background-color: #E0E0E0;
									}

									svg {
										margin-left: 8px;
									}
								}
							}
						}
					}
				}

			}

			&__number {
				display: none;
			}

			&__other {
				display: none;
			}

			&__mapp {
				display: block;

				&__img {}

			}
		}
	}
}
