.talk
{
    margin-bottom: 36px;
    &__header
    {
        margin-top: 12px;
        display: flex;
        align-items: center;
        padding-bottom: 11px;
        border-bottom: 1px solid #e6e6e6;
        &__right
        {
            h4
            {
                color: #333;
                font-size: 16px;

            }

        }
        &__left
        {
            margin-right: auto;
          ul
          {
              display: flex;
              align-items: center;
              li
              {
                  margin-right:12px;
                  &:last-child
                  {
                      border-right: 1px solid #e6e6e6;
                      padding-right: 6px;
                      
                      margin-right:6px;
                     
                  }
                  a
                  {
                    color: #0073bb;
                    font-size: 14px;
                    display: flex;
                    align-items: center;
                    svg
                    {
                        fill: #0073bb ;
                    }
                    &:hover
                    {
                        text-decoration: underline;

                    }
                  }
              }
             
          }
            
        }
    }
    &__item
    {
        ul
        {
            margin-top: 12px;
        
            li
            {
                display: flex;
                align-items: center;
                padding: 17px 0;
                border-bottom: 1px solid #e6e6e6;
            }
        }
        &__avatar
        {
            margin-left: 12px;
            flex: 0 0 60px;
            max-width:60px;
            a
            {
                img
                {
                    width: 60px;
                    height: 60px;
                    border-radius: 4px;
                }
            }
        }
        &__text
        {
            flex: 0 0 calc(100% - 72px);
            max-width:calc(100% - 72px);
            &__title
            {
                display: flex;
                align-items: center;
                margin-bottom: 6px;
               &__right
               {
                   a
                   {
                    color: #0073bb;
                    &:hover{
                        text-decoration: underline;
                    }

                   }
                   span
                   {
                    margin-left: 3px;
                    color: #999;
                   }
               }
               &__left
               {
                   margin-right: auto;
                   small

                   {
                       line-height: 22px;
                       span
                       {
                        font-size: 12px;
                       }
                   }
               }
            }
            &__trust
            {
               
                span
                {
                    color: #333;
                    font-size: 14px;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
      
                }
            }
            &__date
            {
                small
                {
                    color: #999;
                    font-size:12px ;
                    line-height: 14px;
                }
            }
        }
        &__number-page {
            border-bottom: 1px solid #eeeeef;
           padding-top: 11px;
            display: flex;
            align-items: center;

            span {

                margin-right: auto;
                color: #333;
                font-size: 14px;
              
            }



            &__number {
                >ul {
                    display: flex;
                    align-items: center;

                    >li {
                        padding: 0 6px;
                        border-bottom: none;
                        &:last-child
                        {
                            border-bottom:3px solid  #d32323;
                            a
                            {
                                &:hover{
                                    text-decoration: none;
                                }
                            }
                        }

                        a {
                            color: #0073bb;
                            height: 30px;
                            font-size: 14px;
                            line-height: 22px;
                            font-weight: 400;
                            &:hover{
                                text-decoration: underline;
                            }
                            svg
                            {
                                fill:#0073bb;;
                            }
                        }
                    }
                }
            }

            &__cunter {
                margin-right: auto;

                span {
                    color: #757280;
                    height: 30px;
                    font-size: 16px;
                    line-height: 22px;
                    font-weight: 400;
                }
            }

        }
  
    }
    &__content
    {
        &__search {

            display: flex;
            align-items: center;
            background-color: #fff;
            border-bottom: 1px solid #ccc;
            padding: 12px 0;
            margin-bottom: 12px;
            &__input {
                
                height: 38px;
                display: flex;
                align-items: center;
                width: 100%;


                input {
                    // border: none;
                    line-height: 48px;
                    border-radius: 4px;
                    padding: 0 16px;
                    border-radius: 0 4px 4px 0;
                    border: 1px solid #585252;
                    line-height: 28px;
                    border-left: none;
                    width:100%;
                    &:focus

                    {
                        box-shadow: 0 0 4px rgba(0,115,187,0.3);
                        outline: none;
                        color: #333;
                        border-color: #0073bb;
                    }
                }

                button {
                    background: #d32323;
                    border: 1px solid #d32323;
                    width:36px;
                    line-height:26px;
                    border-radius: 4px 0 0 4px;

                    svg {
                        fill: #fff;
                    }
                }
            }

        }
        &__new-talk
        {
            margin-bottom: 24px;
            a
            {
                line-height: 28px;
                transition: all 0s;
                background: #d32323;
                border: 1px solid #d32323;
                color: white;
                font-size: 12px;
                display:flex;
                justify-content: center;
                border-radius: 3px;
                &:hover
                {
                    background-color: #dc4f4f;
                    border: 1px solid #dc4f4f;

                }
            }
        }
        &__title
        {
            margin-bottom: 12px;
            display: flex;
            align-items: center;
            h3
            {
                color: #333;
                font-size: 16px;
            }
            svg
            {
                fill:#333;
                margin-right: auto;
                display: none;
            }
        }
    
        &__list-link
        {
            ul
            {
                li
                {
                    border-bottom: 1px solid #e6e6e6;
                    padding: 12px ;
                    &:hover
                    {
                       background: #f5f5f5;
                    }
                    &:first-child
                    {
                        background: #f5f5f5;
                        border-top: 1px solid #e6e6e6;
                        border-bottom: 1px solid #e6e6e6;
                        border-right: 3px solid  #d32323;
                        padding: 12px;
                        a

                        {
                            display: flex;
                            flex-direction: column;
                          
                            span
                            {
                                color: #333;
                                line-height: 18px;
                             
            
                            }
                            small
            
                            {
                                color: #999;
                                line-height: 16px;
            
                            }
                        }
                        
                    }

                    a
                    {
                        color: #333;
                     line-height: 16px;
                     display: flex;
                     flex-direction: column;
                     span
                     {
                        color: #999;
                        font-size: 12px;
                        line-height: 14px;
                     }
                


                    }
                }
            }
        }
    }
}

@media(max-width:992px)
{
    .talk
    {
        &__content
        {
            &__title
            {
                border-bottom: 1px solid #e6e6e6;
                border-top: 1px solid #e6e6e6;
                padding: 10px 0;
                svg
                {
                    display: flex;
                }
            }
            &__list-link
            {
                display: none;
            
            }
        }
        &__header
        {
           display:none;
        }
      
        &__item
        {
            ul
            {
                li
                {
                    align-items: end;
                }
            }
            &__number-page
            {
                display: flex;
                flex-direction: column;
                &__number
                {
                    margin: 0px 0 -7px 0px;
                   ul
                   {
                    justify-content: center;
                    margin-bottom: 6px;
                   }
                }
                &__cunter
                {
                    text-align: center;
                    order: -1;
                    margin: unset;
                }
            }
            &__text
            {
                &__title
                {
                    display: block;
                }

            }
        }
    }
}

