.footer-business {
	border-top: 1px solid #ccc;
	background-color: #f5f5f5;
	display: flex;

	&__top {
		margin-top: 15px;
		display: flex;
		align-items: center;

		&__right {
			width: 800px;
			margin-left: 24px;
			padding-bottom: 16px;
			&__nav {
				margin-bottom: 12px;

				ul {
					display: flex;
					align-items: center;

					li {
						margin-left: 12px;

						&:last-child {
							margin-left: 0;
						}

						a {
							color: #0073bb;
							font-size: 12px;
							line-height: 16px;
							font-weight: 500;
							white-space: nowrap;
							text-overflow: ellipsis;
							overflow: hidden;

							&:hover {
								text-decoration: underline;
							}

						}
					}
				}
			}

			&__bottom {

				display: flex;
				justify-content: space-between;
				align-items: center;

				&__language {
					display: flex;
					align-items: center;

					select {}

					option {
						font-weight: 400;
						font-size: 14px;
						line-height: 20px;
					}

					.dropdown-menu {
						width: auto;
						border-radius: 0;
						box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
					}

					.form-control {
						background-color: transparent;
					}

					.bootstrap-select:not(.input-group-btn),
					.bootstrap-select[class*=col-] {
						display: flex;
						align-items: center;

					}


					.btn {
						padding: 0;
					}

					.bootstrap-select .dropdown-toggle .filter-option {
						padding-left: 10px;
					}


					.bootstrap-select .dropdown-menu {

						ul {
							li {
								line-height: 16px;
								margin-bottom: 0;
							}
						}
					}

					.bootstrap-select>.dropdown-toggle {
						background-color: transparent;
						border: transparent;
						width: auto;
					}

					.bootstrap-select>.dropdown-toggle:hover .filter-option-inner-inner {
						text-decoration: underline;
						text-align: right;

					}

					.btn-light:not(:disabled):not(.disabled).active,
					.btn-light:not(:disabled):not(.disabled):active,
					.show>.btn-light.dropdown-toggle {
						color: #0073bb;
						background-color: transparent;
						border-color: transparent;
						border: none;
						outline: none;
						box-shadow: none;

					}

					.btn-light {
						color: #0073bb;
					}

					.bootstrap-select .dropdown-toggle:focus,
					.bootstrap-select>select.mobile-device:focus+.dropdown-toggle {
						outline: none !important;
						box-shadow: none;
					}

					.filter-option-inner-inner {
						text-align: right;
						font-weight: 400;
						font-size: 14px;
						line-height: 20px;
					}

					.dropdown-menu {
						.inner {
							.show {
								text-align: right;
							}
						}

						.selected {
							&.active {
								background-color: #0073bb;
							}
						}

					}

					span {
						font-size: 14px;
						color: #333;
						font-weight: 500;
						line-height: 15px;
						margin-left: 12px;

					}
				}
			}

		}

		&__left {
			display: flex;
            align-items: center;
            background: url('../img/img-footer.png') no-repeat calc(100% - 12px) 0 / 70px;
            padding-bottom: 16px;
            min-height: 70px;
            padding-right: 95px;
			&__text {
				display: flex;
				align-items: center;

				p {

					margin-bottom: 0;
				}

			}

			&__image {
				margin-left: 12px;

				img {
					height: 70px;
					width: 95px;
				}
			}

		}


	}

	&__bottom {
		text-align: center;
		border-top: 1px solid #ccc;

		&__image {
			margin-bottom: 18px;
			margin-top: 42px;
			text-align: center;

		}

		&__copyright {
			padding-bottom: 18px;
			text-align: center;
			display: flex;
			align-items: center;
			justify-content: center;
			flex-wrap: wrap;


			span {
				line-height: 14px;
				font-size: 12px;

			}

			img {
				&:first-of-type {

					width: 38px;
					height: 19px;

				}

				width: 13px;
				height: 19px;
				margin: 0 4px;

			}
		}
	}
}


@media(max-width:992px) {
	.footer-business {
		&__top {
			display: block;

			&__right {
				width: 100%;

				&__nav {
					ul {
						flex-wrap: wrap;
					}
				}
			}

			&__left {}
		}

	}
}

@media(max-width:480px) {
	.footer-business {
		&__top {
			display: block;

			&__right {
				width: 100%;

				&__nav {
					ul {
						flex-wrap: wrap;
					}
				}

				&__bottom {
					display: block;

					&__language {
						margin-bottom: 12px;
						margin-top: 6px;

						span {
							display: none;
						}

						.btn-light {
							color: #000;
						}

						.bootstrap-select>.dropdown-toggle {
							background: rgba(250, 250, 249, 0.9);
							border: 1px solid #ccc;
							border-radius: 3px;
							width: 100%;
						}

						.bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn) {
							width: 100%;
						}

						.filter-option-inner-inner {


							line-height: 27px;
						}

						.bootstrap-select>.dropdown-toggle {
							padding: 0 10px;

						}
					}

					&__phone {
						background: rgba(250, 250, 249, 0.9);
						border: 1px solid #ccc;
						border-radius: 3px;
						width: 100%;
						text-align: center;
						line-height: 43px;

					}

				}
			}
		}

		&__bottom {
			padding-top: 18px;
			margin-bottom: 40px;

			&__image {
				display: none;
			}
		}

	}
}
