.profile-content
{
    margin-bottom: 36px;
  &__list-profile
    {
        &__title
        {
        margin: 50px 0 12px 0;
        display: flex;
        align-items: center;
          h3
          {
            color: #333;
            font-size: 16px;
            line-height: 21px;
          }
          svg
          {
              margin-right: auto;
              display:none;
          }
       
        }
        ul
        {
              
            margin-bottom:36px;
                li
                {
                    &:first-child
                    {
                        background: #f5f5f5;
                        border-right: 3px solid  #d32323;
                        border-top: 1px solid #e6e6e6;

                    }
                    border-bottom: 1px solid #e6e6e6;
                    padding: 11px;
                   
                    &:hover
                    {
                       background: #f5f5f5;
                    }

                    a
                    {
                    color: #333;
                    //  line-height: 16px;
                     display: flex;
                     align-items: center;
                     svg
                     {
                       fill: #666;
                       font-size: 24px;
                       margin-left: 3px;
                     }
                  
                    }
                }
        }
        &__report
        {
            a
            {
                color: #999;
                &:hover{
                    text-decoration: underline;
                }
                
                svg
                {
                    font-size: 14px;
                    fill: #666;

                }
            
            }
        } 
    }

  &__comment
  {
    border-left: 1px solid #e6e6e6;
    padding: 0 15px;


      &__title
      {
          h2
          {
            color: #d32323;
            margin-bottom:24px;
            font-size: 21px;

          }
      }
      &__filter {
        display: flex;
        align-items: center;
        margin-right: auto;
        border-bottom: 1px solid #e6e6e6;
        padding-bottom:6px;
        :hover
        {
            position: relative;
            &::before
            {
                content: '';
                position: absolute;
                bottom:-8px;
                left:0%;
                border:1px solid #d32323;
                width:170px;
             }
        }
        

        span {
            width: auto;
            margin-left: 6px;
            font-size: 12px;
            text-align: right;

        }

        select {
         
        }

        option {
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            color: #00838f;
        }

        .bootstrap-select:not(.input-group-btn),
        .bootstrap-select[class*=col-] {
            display: flex;
            align-items: center;

        }

        .bootstrap-select.form-control:not([class*=col-]) {
            width: unset;

        }


        .btn {
            padding: 0;
        }

        .bootstrap-select .dropdown-toggle .filter-option {
            padding-left: 10px;
        }


        .bootstrap-select .dropdown-menu {
            box-shadow: 0 0 18px rgba(0, 0, 0, .15);
        }

        .bootstrap-select>.dropdown-toggle {
            background-color: transparent;
            border: transparent;

        }

        .bootstrap-select>.dropdown-toggle:hover .filter-option-inner-inner {
            // text-decoration: underline;

        }

        .btn-light:not(:disabled):not(.disabled).active,
        .btn-light:not(:disabled):not(.disabled):active,
        .show>.btn-light.dropdown-toggle {
            color: #2b273c;
            background-color: transparent;
            border-color: transparent;
            border: none;
            outline: none;
            box-shadow: none;

        }

        .bootstrap-select .dropdown-toggle:focus,
        .bootstrap-select>select.mobile-device:focus+.dropdown-toggle {
            outline: none !important;
            box-shadow: none;
        }

        .filter-option-inner-inner {
            text-align: right;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
        }

        .dropdown-menu {
            left: 0;
            top: 0;
            right: -114px;
            .inner {
                .show {
                    text-align: right;
                }
            }

            .selected {
                &.active {
                    background-color:#666;
                }
            }

        }
        .dropdown-item:hover
        {
            background-color:#666;
            color: #fff;
           

        }
        

    }
    &__comments
     {
    border-bottom: 1px solid #e6e6e6;
    padding: 18px 0;
    &__info
    {   
        margin-bottom: 18px;
        display: flex;
       
        &__image
        {
           margin-left: 12px;
       
            img
            {
                width: 60px;
                height: 60px;
                border-radius: 4px;

            }
        }
      &__text
      {
        &__name
        {
          a
          {
           
            font-size: 14px;
            font-weight: 500;
            color: #0073bb;
            &:hover
            {
                text-decoration: underline;
            }
            
          }
        }
        &__category
        {
            ul
            {
                display: flex;
                align-items: center;
                li
                {
                    margin-left: 6px;
                    a
                    {
                        display: flex;
                        align-items: center;
                        color: #0073bb;
                        &:hover
                        {
                            text-decoration: underline;
                        }

                    }
                }
            }
        }
        &__location
        {
            display: flex;
            flex-direction: column;
            span
            {
                color: #333;
                font-size: 12px;
                line-height: 18px;
               
            }
        }
      
      }
    }
    &__Score{
        display: flex;
        align-items: center;
        margin-bottom: 6px;

        &__star {
            margin-left: 8px;

            width: 108px;
            height: 20px;
            background-image: url(../img/star.png);
            background-repeat: no-repeat;

            &[data-star="1"] {
                background-position: 0 -320px;
            }

            &[data-star="1"] {
                background-position: 0 -360px;
            }

            &[data-star="1.5"] {
                background-position: 0 -340px;
            }

            &[data-star="2"] {
                background-position: 0 -400px;
            }

            &[data-star="2.5"] {
                background-position: 0 -380px;
            }

            &[data-star="3"] {
                background-position: 0 -440px;
            }

            &[data-star="3.5"] {
                background-position: 0 -420px;
            }

            &[data-star="4"] {
                background-position: 0 -480px;
            }

            &[data-star="4.5"] {
                background-position: 0 -460px;
            }

            &[data-star="5"] {
                background-position: 0 -500px;
            }


        }
    }
    &__check
    {
        margin-bottom: 12px;
        svg
        {
            fill: #0077bc;
            font-size: 18px;

        }
        span
        {
            color: #666;
            font-size: 12px;
        }
    }
    &__txt
    {
        p
        {
            color: #333;
            font-size: 14px;
            text-align: justify;

        }
        span
        {
            color: #333;
            font-size: 14px;
        }
        ul
        {
            margin-bottom: 20px;
            li
            {
               
            font-size: 14px;
            text-align: justify;

            }
        }
    }
    &__image
    {
      
        margin-bottom: 12px;
        padding-left: 130px;
        &__img
        {
         
            position: relative;
            margin-bottom: 10px;
            img
            {
                border-radius: 4px;
                width: 100%;
            }
            span
            {
                position: absolute;
                bottom: 0;
                right: 0;
                left: 0;
                padding: 0 15px;
                background: rgba(0 ,0, 0, 0.5);
                color: #fff;
                line-height: 30px;
                font-weight: 500;
                font-size: 14px;
              
            }
        }
   
    }
    &__button-cm
    {
        display: flex;
        align-items: center;
       &__right
       {
        ul
        {
            display: flex;
            align-items: center;
            li
            {
                margin-left: 6px;
                transition: all 0s;
                &:hover

                {
                 background-color: #f5f5f5;
                }
            
                a
                {
                    color: #666;
                    
                    border: 1px solid #ccc;
                    padding: 0 8px;
                    border-radius: 4px;
                    font-size: 12px;
                    display: flex;
                    align-items: center;
                    line-height: 28px;
                    margin-left: 3px;
                   
                    svg
                    {
                        fill:#666;
                        margin-left: 3px;
                        font-size: 18px;
                    }
                    span
                    {
                        color: #666;
                        margin-right: 3px;

                    }
                }
                
            }
        }
       }
       &__left
       {
           margin-right: auto;
        
        ul
        {
            display: flex;
            align-items: center;
            li
            {
                margin-left: 6px;
                transition: all 0s;
               &:hover{
                background-color: #f5f5f5;
               }
            
                a
                {
                    color: #666;
                   
                    border: 1px solid #ccc;
                    padding: 0 8px;
                    border-radius: 4px;
                    font-size: 12px;
                    display: flex;
                   
                    svg
                    {
                        fill:#666;
                        font-size: 18px;
                        height: 30px;
                        display: block;
                    }
                  
                }
                
            }
        }
       }
    }
    &__form
    {
        margin-top: 20px;
        padding: 11px;
    background: #f5f5f5;
    border: 1px solid #e6e6e6;
    border-radius: 4px;
        &__top
        {
            display: flex;
            margin-bottom:12px;

            &__image
            {
                margin-left: 6px;
                img
                {
                    border-radius: 4px;
                    

                }
            }
            &__text
            {
                p
                {
                    margin-bottom: 0;

                }
                span
                {

                }
            }
        }
        &__bottom
        {
            p
            {
                margin-bottom: 0;
                text-align: justify;
                a
                {
                    margin-right: 3px;
                    &:hover{
                        text-decoration: underline;
                    }
                }
            }
        }
    }
     }
     &__more
    {
    border-bottom: 1px solid #e6e6e6;
    padding: 11px 0;
      a
      {
        color: #0073bb;
        &:hover{
            text-decoration: underline;
        }

      }
    }
  }
  &__about
  {
    &__title
    {
        h3
        {
            color: #d32323;
            margin-bottom: 12px;
    font-size: 16px;

        }
    }
    &__score-title
    {
        margin-bottom: 12px;
        h4
        {
            color: #333;
            font-size: 14px;
        }
    }
    &__score
    {
        margin-bottom: 6px;
      
        &__review
        {
            $review:&;
            position: relative;
            height: 20px;
            margin-bottom: 6px;
            >span
            {
                position: absolute;
                right: 10px;
                top: 50%;
                transform: translateY(-50%);
                z-index: 5;
            }
            &__progerss
            {
                background-color: #fcd6d3;
                position: absolute;
                bottom: 0;
                right: 0;
                top: 0;
                z-index: 2;

                span
                {
                    position: absolute;
                    right: 100%;
                    top: 50%;
                    transform: translateY(-50%);
                    padding-right: 6px;
                }

            }
            &:nth-child(1)
            {
                #{$review}__progerss
                {
                    background-color: #fcd6d3;
                }
            }
            &:nth-child(2)
            {
                #{$review}__progerss
                {
                background-color:#FCDFD1;
                }
            }
            &:nth-child(3)
            {
                #{$review}__progerss
                {
                background-color:#FDE6CF;
                }
            }
            &:nth-child(4)
            {
                #{$review}__progerss
                {
                background-color:#FEEECD;
                }
            }
            &:nth-child(5)
            {
                #{$review}__progerss
                {
                background-color:#FEF4CC;
                }
            }
        }
    }
    &__more-score
    {
        margin-bottom: 18px;
        a
        {
            color: #0073bb;
            &:hover
            {
                text-decoration: underline;
            }
        }
    }
    &__review-votes
    {
        margin-bottom: 18px;
        &__title
        {
            margin-bottom: 12px;
         h4
         {
           
            font-size: 14px;
            color: #333;
         }
       
     }
     ul
     {
         li
         {
            color: #333;
            margin-bottom: 6px;
             svg
             {
                 font-size: 18px;
                 fill: #666;
                 margin-left:3px;

             }
             span
             {
                 margin-right:3px;
                 color: #333;
                 font-weight: 500;

             }
         }
     }
    }
    &__stats
    {
        margin-bottom: 18px;

        &__title
        {
            margin-bottom: 12px;
            h4
            {
                color: #333;
                font-size: 14px; 
            }
        }
        ul
        {
            li
            {
               color: #333;
               margin-bottom: 6px;
                svg
                {
                    font-size: 18px;
                    fill: #666;
                    margin-left:3px;

                }
                span
                {
                    margin-right:3px;
                    color: #333;
                    font-weight: 500;

                }
            }
        }
    }
    &__compliments
    {
        margin-bottom: 18px;

        &__title
        {
            h4
            {
                color: #333;
                margin-bottom: 12px;
               font-size: 14px;
            }
          
        }
        ul
        {
            display: flex;
            li
            {
                display: flex;
                margin-left:12px;
                flex-direction: column;
                text-align: center;
                font-size: 12px;
                color: #333;
                font-weight: 400;
              
                &:nth-child(1)
                {
                   svg
                   {
                    background-color: #f15c00;
                   }
                }
                &:nth-child(2)
                {
                   svg
                   {
                     background-color: #f15c00;
                   }
                }
                &:nth-child(3)
                {
                   svg
                   {
                    background-color: #666666;
                   }
                }
                &:nth-child(4)
                {
                   svg
                   {
                    background-color: #0073bb;
                   }
                }
                &:nth-child(5)
                {
                   svg
                   {
                    background-color: #0097ec;
                   }
                }
              
                svg
                {
                    font-size: 18px;
                  fill: #fff;
                  height: 24px;
                  width: 24px;
                  padding: 3px;
                  border-radius: 4px;
                }
            }
        }
    }
    &__location
    {
        margin-bottom: 18px;
        &__title
        {
            h4
            {
                color: #333;
                margin-bottom: 6px;
                font-size: 14px;

            }
        }
        &__content
        {
            p
            {
                margin-bottom: 12px;
                
            }
        }
    }
    &__since
    {
        margin-bottom: 18px;
        &__title
        {
            h4
            {
                color: #333;
                margin-bottom: 6px;
                font-size: 14px;

            }
        }
        &__content
        {
            p
            {
                margin-bottom: 12px;
                
            }
        }
    }
  }
  &__mobile-about
  {
    margin-left: -10px;
    margin-right: -10px;
      display: none;
   &__list1
   {
       margin-bottom: 30px;
       border-bottom: 1px solid #e6e6e6;
    ul
    {
        li
        {
            background-color: #fff;
              padding: 10px;
              border-top: 1px solid #e6e6e6;
              display: flex;
              position: relative;
              &:first-child
              {
                  border-top: none;
              }
              &:last-child
              {

              }
              a
              {
                  color: #333;
                  
                  &:before
                  {position: absolute;
                      content: "";
                      top:0 ;
                      bottom:0 ;
                      left: 0;
                      right: 0;
                  }
                  &:hover
                  {
                      text-decoration: underline;
                  }

                  svg
                  {
                      fill: #f25210;
                      width: 24px;
                      height: 24px;
                      margin-left: 10px;
                  }
                  span
                  {
                      margin-right: 4px;
                  color: #666;

                  }

              }
          
        }
     
    }
    &__svg-left
    {
      margin-right: auto;
        svg
        {
            fill: #ccc;

        }
    }
   }
   &__list2
   {
    margin-bottom: 30px;
    border-bottom: 1px solid #e6e6e6;
       
    ul
    {
        li
        {
            background-color: #fff;
              padding: 10px;
              border-top: 1px solid #e6e6e6;
              display: flex;
              position: relative;
           
          
              a
              {
                  color: #333;
                  &:before
                  {position: absolute;
                      content: "";
                      top:0 ;
                      bottom:0 ;
                      left: 0;
                      right: 0;
                  }
                  &:hover
                  {
                      text-decoration: underline;
                  }

                  svg
                  {
                    fill: #666;
                      width: 24px;
                      height: 24px;
                      margin-left: 10px;
                  }
                  span
                  {
                      margin-right: 4px;
                  color: #666;

                  }

              }
          
        }
     
    }
    &__svg-left
    {
      margin-right: auto;
        svg
        {
            fill: #ccc;

        }
    }
   }
  }
}

@media(max-width:767px)
{
    .profile-content
    {
        &__about
        {
            &__score
            {
                margin-bottom: 20px;
            }
            &__title
            {
                margin-top:15px;
            }
        }
        &__list-profile
        {

            &__title
            {
                margin: 20px 0 30px 0;
                border-bottom: 1px solid #e6e6e6;
                border-top: 1px solid #e6e6e6;
                padding: 10px 0;
                &:hover
                {
                    background-color:#e6e6e6;
                    cursor: pointer;
                }
                svg
                {
                    display:flex;
                }
               
            }
            ul
            {
                display:none;
            }
            &__report
            {
                display:none;
            }
        }
        &__comment
        {
            border-left: none;
        
        }
    }
}

@media(max-width:576px)
{
    .profile-content
    {
        margin-bottom:0;
        background: #f5f5f5;
        &__comment
        {
            &__comments
            {
                
                &__button-cm
                {
                    display: block;
                    &__right
                    {
                        margin-bottom:6px;
                        ul
                        {
                            justify-content: center;
                            li
                            {
                                margin-left: 3px;
                                a
                                {
                                    padding: 0 4px;
                                }
                            }
                        }

                    }
                    &__left
                    {
                        ul
                        {
                            justify-content: center;
                        }
                    }
                }
                &__image
                {
                    padding-left:0;
                }
            }
        }
        &__about
        {
            display: none;

        }
        &__mobile-about
        {
            display: block;
        }
    }
}