.footer-mobile1
{
    display: none;
    padding-top:30px;
    border-top: 1px solid #ccc;
    background-color: #f5f5f5;



    &__mobile
    {
        &__country
        {
            margin-bottom: 30px;
            .form-select
            {
                border: 1px solid #ccc;
                padding: 0 9px;
                border-radius: 3px;
                width: 100%;
                font-size: 14px;
                 height: 37px;

            }
        }
        &__language
        {
            margin-bottom: 30px;
            .form-select
            {
                border: 1px solid #ccc;
                padding: 0 9px;
                border-radius: 3px;
                width: 100%;
                font-size: 14px;
                 height: 37px;

            }


        }
        &__menu
        {
            ul
            {
                display:flex;
                justify-content: center;
                li
                {
                    margin-left: 12px;
                    a
                    {
                        font-size: 16px;
                        color: #0073bb;
                        &:hover
                        {
                            text-decoration: underline;
                        }

                    }
                }
            }
        }
        &__copyright
        {
            padding-bottom: 30px;
            span
            {
                margin-top: 18px;
                text-align: center;
                display: block;

            }
        }
    }
    
}

@media(max-width:576px)
{
    .footer-mobile1
    {
        display:block;
      
    }
}