.recent-collections {
    margin-top: 36px;

    &__title {
        margin-bottom: 12px;
        text-align: center;

        h2 {
            font-size: 16px;
            line-height: 21px;
            color: #d32323;
            font-weight: 500;
        }
    }

    &__content {
        $self: &;
        padding-top: 11px;
        border: 1px solid #e6e6e6;
        border-radius: 4px;

        ul {

            display: flex;
            align-items: center;
            flex-wrap: wrap;

            li {
                padding: 0 15px;
                margin-bottom: 12px;
                display: flex;
                flex: 0 0 50%;
                max-width: 50%;

                &:nth-child(5),
                &:nth-child(6) {
                    #{$self}__wrapper {
                        border-bottom: none;
                        margin-bottom: 0;
                        padding-bottom: 0;

                    }

                }

            }
        }

        &__wrapper {

            display: flex;
            border-bottom: 1px solid #e6e6e6;
            padding-bottom: 11px;



            &__image {
                margin-left: 12px;
                position: relative;
                flex: 0 0 72px;
                max-width: 72px;


                >a {
                    img {
                        width: 72px;
                        height: 72px;
                        border-radius: 3px;
                    }
                }

                &__icon-save {
                    background-color: rgba(0, 0, 0, .3);
                    position: absolute;
                    height: 50%;
                    width: 50%;
                    bottom: 0;
                    right: 0;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    backdrop-filter: blur(2px);

                    >a {


                        svg {
                            fill: #fff;
                        }
                    }
                }
            }

            &__text {
                display: flex;
                flex-direction: column;
                flex: 0 0 calc(100% - 72px);
                max-width: calc(100% - 72px);

                >a {
                    font-weight: 500;
                    color: #0073bb;
                    font-size: 14px;
                    line-height: 17px;
                    height: 17px;



                    &:hover {

                        text-decoration: underline;
                    }
                }

                p {
                    margin-bottom: 0;
                    line-height: 18px;
                    font-size: 12px;
                    height: 36px;
                    overflow: hidden;

                }

                span {
                    line-height: 16px;
                    height: 17px;

                    >a {

                        color: #0073bb;

                        &:hover {
                            text-decoration: underline;
                            color: #0073bb;
                        }
                    }
                }
            }
        }
    }

    &__title-bottom {
        margin-top: 12px;
        margin-bottom: 12px;
        text-align: center;
        font-size: 14px;

        a {
            margin-bottom: 12px;
            color: #0073bb;

            &:hover {
                text-decoration: underline;
                color: #0073bb;
            }
        }
    }

}

@media(max-width: 480px) {
    .recent-collections {
        display: none;
    }

}

@media(max-width: 576px) {
    .recent-collections {
        display: none;
    }

}