.adpartners-contact
{
    position: relative;
    padding: 50px 0;
    &__content
    {
        background-color: #f5f5f5;
        padding: 5px 15px;
        color: #6B6E81;
        text-align: center;
        line-height: 25px;
        font-weight: 600;
        margin-bottom: 10px;
        a
        {
            color: #14A5D7;
        }
    }
    &__box
    {
        display: flex;
        align-items: center;
        flex-direction: column;
        border-bottom: 1px solid #D4D4D4;
        padding: 15px 0;
        span
        {
            display: flex;
            line-height: 25px;
            font-size: 14px;
            
            margin-bottom: 10px;
            &:first-child
            {
                font-weight: 600;
            }
            a
            {
                color: #14A5D7;
                margin: 0 5px;
                font-weight: 400;
            }
        }
    }
}