.profile-header
{
    background-color: #f5f5f5;
    background-image: url('../img/profile-header-bg.png');
    background-position: bottom;
    background-size: 1400px 268px;
    background-repeat: no-repeat;

    border-bottom: 1px solid #e6e6e6;
    padding-top: 15px;
    height: 195px;
    margin-bottom: 15px;
    
    &__user-profile
    {
        display: flex;

        &__image
        {
            margin-top: 9px;
            width: 215px;
            height:215px;
            margin-left: 30px;
            img
            {
                max-width: 100%;
                max-height: 100%;
                border-radius: 4px;
                
            }
        }
        &__info
        {
            margin-top: 30px;
            &__title
            {
                margin-bottom: 6px;
                h1
                {
                    margin-bottom: 6px;
                    font-size: 30px;
                    color: #333;
                    line-height: 36px;

                }
            }
            &__location
            {
                margin-bottom: 6px;
                h3
                {
                    font-size: 16px;
                    color: #333;
                    line-height: 21px;
                    font-weight: 400;
                }


            }
            &__stats
            {
                margin-bottom: 6px;
             ul
             {
                 display: flex;
                 align-items: center;
                 li
                 {
                     display: flex;
                     align-items: center;
                     margin-left:12px;

                    svg
                    {
                        fill: #f15c00;
                        font-size: 24px;
                        margin-left: 4px;
    
                    }
                    span
                    {
                        font-weight: 500;
                        color: #333;
                        margin-left:4px;
                        font-size: 14px;
    
                    }
                    small
                    {
                        color: #333;
                        font-size: 12px;
    
                    }
                 }
             }
            }
            &__elite
            {
                display: flex;
                align-items: center;
                &__button
                {
                    margin-left: 6px;
                    border-radius: 4px;
                    background-color: #d32323;
                    border: 1px solid #b91f1f;                   
                    a
                    {
                    
                        line-height: 20px;
                        border-left: 1px solid #b91f1f;
                        padding: 0 2px;
                        color: #fff;
                        &:last-child
                        {
                        border-left: none;
                        }
                    }
                }
                &__link
                {
                    a
                    {
                        font-size: 12px;
                        color: #0073bb;
                       &:hover
                       {
                        text-decoration: underline;
                       }
                    }
                }
            }
        }
    }
    &__list-action
    {
        ul
        {
            border-right: 1px solid #e6e6e6;
            padding: 6px 15px 6px 0px;

            li
            {
            
                a
                {
                    color: #0073bb;
                    font-weight: 500;
                    line-height: 28px;
                    &:hover
                    {
                        text-decoration: underline;
                    }
                    svg
                    {
                        margin-left: 12px;
                        font-size: 18px;
                        fill: #0073bb;
                    }
                }
            }
        }
    }
}


@media(max-width:767px)
{
    .profile-header
    {
        height: unset;
        &__list-action
        {
            margin: 15px 0px;
            ul
            {
                display: flex;
                border-right:none;
                padding: 0;
                justify-content: center;

                li
                {
                    margin-left:12px;
                    a
                    {
                        font-size: 10px;
                        svg
                        {
                            margin-left:0px;
                        }
                    }
                }
            }
        }
    }
}

@media(max-width:576px)
{
    .profile-header
    {
        margin-bottom: 0;
        &__list-action
        {
            margin: 15px 0px;
            ul
            {
                display: flex;
                border-right:none;
                padding: 0;
                justify-content: center;
                li
                {
                    text-align: center;
                    a
                    { 
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        font-size: 11px;
                        svg
                        {
                            margin: 0 auto;
                        }
                    }
                }
            }
        }
        &__user-profile
        {
            &__image
            {
                margin-left:15px;
            }
        }
       
    }
}

@media(max-width:480px)
{
    .profile-header
    {
        
        &__user-profile
        {
            display: block;
            text-align: center;
            &__image
            {
                flex: 0 0 100%;
                max-width: 100%;
                height: 129px;
                width: unset;
                margin-left: 0;
                margin-top:0;
            }
            &__info
            {
                margin-top: 15px;
                &__title
                {
                    h1
                    {
                        font-size: 21px;
                    }
                }
                &__stats
                {
                    display: none;
                }
                &__location
                {
                    h3
                    {
                        font-size: 12px;
                    }
                }
                &__elite
                {
                    display: none;
                }
            }

        }
        &__list-action
        {
            ul
            {
                flex-direction: column;
               li
               {
                &:nth-child(1)
                {
                    display:none;
                }
                &:nth-child(4)
                {
                    display:none;
                }
                &:nth-child(5)
                {
                    display:none;
                }
                &:nth-child(2)
                {
                    border: 1px solid #ccc;
                    background: #fff;
                    margin-bottom:12px;
                   a
                   {
                       display: block;
                       color: #666;
                       line-height: 41px;
                       &:hover
                       {
                           text-decoration: none;
                       }
                       svg
                       {
                           fill:#666;
                       }
                   }
                }
                &:nth-child(3)
                {
                    border: 1px solid #ccc;
                    background: #fff;
                    margin-bottom:12px;
                   a
                   {
                       display: block;
                       color: #666;
                       line-height: 41px;
                       &:hover
                       {
                           text-decoration: none;
                       }
                       svg
                       {
                           fill:#666;
                       }
                   }
                }
               }
            }
        }
    }
}

