.adpartners-intro
{
    min-height: 330px;
    position: relative;
    padding-top: 50px;
    &__background
    {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 1;
        background-image: url('../img/headerbg.png');
        background-repeat: repeat-x;
        background-position: center left;
        background-size: 850px;
        opacity: 0.72;
    }
    .container
    {
        position: relative;
        z-index: 2;
    }
    &__wrapper
    {
        background-color: #fff;
        margin: 0 auto;
        padding: 10px 25px 20px;
        border-radius: 3px;
        text-align: center;
        h1
        {
            color: rgb(211, 35, 35);
            font-size: 32px;
            line-height: 45px;
            font-weight: 600;
            margin-bottom: 18px;
        }
        p
        {
            font-size: 14px;
            color: #6B6E81;
            line-height: 27px;
            margin-bottom: 18px;
        }
        a
        {
            background-color: #ff122c;
            color: #fff;
            line-height: 44px;
            border-radius: 22px;
            padding: 0 35px;
            display: inline-block;
            font-size: 13px;
            transition: 0.3s;
            &:hover
            {
                background-color: #de0018;
            }
        }
    }
}


@media(max-width: 576px)
{
    .adpartners-intro
    {
        &__wrapper
        {
            h1
            {
                font-size: 20px;
                line-height: 30px;
            }
        }
    }
}