.upcoming-events
{
    border-bottom: 1px solid #e6e6e6;
    margin-bottom: 30px;
    &__item
    {
        
        &__title
        {
         
            h2
            {
                color: #d32323;
                font-size: 21px;
            }
        }
        ul
        {
           margin-bottom: 12px;
            li
            {
                display: flex;
                padding: 18px 0;
                border-bottom: 1px solid #e6e6e6;
             

            }
        }
        &__image
        {
            flex: 0 0 120px;
            max-width: 120px;
            height: 120px;
           
            img
            {
                width: 100%;
                height: 100%;
                border-radius: 4px;
            }
        }
        &__text
        {
            flex: 0 0 calc(100% - 120px);
            max-width: calc(100% - 120px);
            padding-right:6px ;
            &__top
            {
              
                margin-bottom: 12px;
                &__title
                {
                    margin-bottom: 6px;
                    a
                    {
                        color: #0073bb;
                        font-size:14px;
                        font-weight: 500;
                        line-height:17px;
                        &:hover{
                            text-decoration: underline;
                        }
                    }
                }
                &__date
                {
                    height: 24px;
                    overflow: hidden;
                    margin-bottom: 6px;

                    svg
                    {
                        fill: #666;
                    }
                    span
                    {
                        color: #333;
                        line-height: 24px;
                    }
                }
                &__location
                {
                    margin-bottom: 6px;
                    svg
                    {
                        fill: #666;
                    }

                    span
                    {
                        color: #333;
                    }
                }
                &__desc
                {
                    p
                    {
                        text-align: justify;
                        color: #333;
                        margin-bottom: 0;
                        line-height: 17px;
                        height: 34px;
                        overflow: hidden;
                        margin-bottom: 12px;
                  
                    }
                }
            }
            &__bottom
            {
                padding:0 12px 12px 12px;
                display: flex;
                align-items: center;
                a
                {
                    color: #0073bb;
                    margin-left: 6px;
                    line-height: 16px;
                    &:hover
                    {
                        text-decoration: underline;
                    }

                }
                span
                {
                    color: #999;
                    line-height: 16px;

                }
            }
        }
        &__view-all
        {
            a
            {
                color: #0073bb;
                &:hover{
                    text-decoration: underline;
                }
            }
        }
       
    }
    &__content
    {
        border-right: 1px solid #e6e6e6;
        margin-right: -12px;
        padding-right: 12px;
        &__title
        {
            h3
            {
                color: #d32323;
                font-size: 16px;
                font-weight: 500;
            }
        }
        ul
        {
        
            li
            {
                display: flex;
                padding: 12px 0;
            
            }
        }
        &__image
        {
            flex: 0 0 60px;
            max-width: 60px;
            height: 60px;
            
            img
            {
                width: 100%;
                height: 100%;
                border-radius: 4px;
            }
        }
        &__text
        {
            flex: 0 0 calc(100% - 60px);
            max-width:calc(100% - 60px) ;
            padding-right:6px ;
            &__title
            {
             
                
                a
                {
                    color: #0073bb;
                    font-size:14px;
                    font-weight: 500;
                    line-height:25px;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    display: block;
                    &:hover{
                        text-decoration: underline;
                    }
                }
            }
            &__date
            {
            
                svg
                {
                    fill: #666;
                }
                span
                {
                    color: #333;
                    font-size: 12px;
                    line-height: 12px;
                }
            }
            &__interested
            {
                span
                {
                    color: #999;
                    font-size: 12px;
                    line-height: 14px;
                }
            }
        }
    }
}

@media(max-width:992px)
{
    .upcoming-events
    {
        &__item
        {
            &__view-all
            {
                margin-bottom: 20px;

            }
        }
        &__content
        {
            border-right:none;
            margin-right:0px;
            padding-right:0px;
        }
    }
}



@media(max-width:576px)
{
    .upcoming-events
    {
        margin-bottom: 0;
        &__item
        {
            margin-bottom: 30px;
            &__image
            {
                flex: 0 0 80px;
                max-width: 80px;
                img
                {
                    height: 80px;
                }
            }
            &__text
            {
                flex: 0 0 calc(100% - 80px);
                max-width: calc(100% - 80px);
                &__top
                {
                    &__title
                    {

                        a
                        {
                            font-size: 14px;
                        }
                    }
                    &____date
                    {
                        span
                        {
                            font-size: 12px;
                        }
                    }
                    &__location
                    {
                        font-size: 12px;

                    }
                    &__desc
                    {
                        p
                        {
                            font-size: 12px;

                        }
                    }
                }
            }
        }
        &__content
        {
           ul
           {
              margin-bottom: 15px;
           }
        }
    }
}