.footer-type1
{
    border-top: 1px solid #ccc;
    padding-top: 36px;
    font-size: 14px;
    line-height: 1.28571em;
    background-color: #f5f5f5;
    &__ln
    {
        &__item {
            select {
                
            }

            option {
                font-weight: 400;
                font-size: 14px;
                line-height: 20px;
            }
            .dropdown-menu
            {
               width: auto;
               border-radius: 0;
               box-shadow: 0 2px 8px rgba(0,0,0,0.2);
            }
            .form-control
            {
                background-color:transparent;
            }

            .bootstrap-select:not(.input-group-btn),
            .bootstrap-select[class*=col-] {
                display: flex;
                align-items: center;

            }


            .btn {
                padding: 0;
            }

            .bootstrap-select .dropdown-toggle .filter-option {
                padding-left: 10px;
            }


            .bootstrap-select .dropdown-menu {
               
                ul
                {
                    li
                    {
                        line-height:16px;
                        margin-bottom: 0;
                    }
                }
            }

            .bootstrap-select>.dropdown-toggle {
                background-color: transparent;
                border: transparent;
                width: auto;
                margin-bottom: 24px;
            }

            .bootstrap-select>.dropdown-toggle:hover .filter-option-inner-inner {
                text-decoration: underline;
                text-align:right;

            }

            .btn-light:not(:disabled):not(.disabled).active,
            .btn-light:not(:disabled):not(.disabled):active,
            .show>.btn-light.dropdown-toggle {
                color: #0073bb;
                background-color: transparent;
                border-color: transparent;
                border: none;
                outline: none;
                box-shadow: none;

            }
            .btn-light
            {
                color: #0073bb;
            }

            .bootstrap-select .dropdown-toggle:focus,
            .bootstrap-select>select.mobile-device:focus+.dropdown-toggle {
                outline: none !important;
                box-shadow: none;
            }

            .filter-option-inner-inner {
                text-align: right;
                font-weight: 400;
                font-size: 14px;
                line-height: 20px;
            }

            .dropdown-menu {
                .inner {
                    .show {
                        text-align: right;
                    }
                }

                .selected {
                    &.active {
                        background-color: #0073bb;
                    }
                }

            }

            &__title {
                margin-bottom: 12px;
    
                h2 {
                    font-weight: 500;
                    margin-bottom: 6px;
                    font-size: 16px;
                    line-height: 21px;
                    color: #d32323;
                }
            }
    
            ul {
                margin-bottom: 24px;
    
    
    
                li {
    
                    margin-bottom: 6px;
    
                    a {
                        color: #0073bb;
    
                        &:hover {
                            text-decoration: underline;
                        }
                    }
                }
            }
    
            &__language {
                margin-bottom: 24px;
    
                h2 {
                    color: #d32323;
                    font-weight: 500;
                    margin-bottom: 6px;
                    font-size: 16px;
                    line-height: 21px;
                    color: #d32323;
                    margin-bottom: 12px;
                }
                ul {
                    margin-bottom: 24px;
        
        
        
                    li {
        
                        margin-bottom: 6px;
        
                        a {
                            color: #0073bb;
        
                            &:hover {
                                text-decoration: underline;
                            }
                        }
                    }
                }
            }
    
            &__Countries {
                margin-bottom: 24px;
    
                h2 {
                    color: #d32323;
                    font-weight: 500;
                    margin-bottom: 6px;
                    font-size: 16px;
                    line-height: 21px;
                    color: #d32323;
                    margin-bottom: 12px;
                }
                ul {
                    margin-bottom: 24px;
        
        
        
                    li {
        
                        margin-bottom: 6px;
        
                        a {
                            color: #0073bb;
        
                            &:hover {
                                text-decoration: underline;
                            }
                        }
                    }
                }
            }
        }
    }

    &__item {

        &__title {
            margin-bottom: 12px;

            h2 {
                font-weight: 500;
                margin-bottom: 6px;
                font-size: 16px;
                line-height: 21px;
                color: #d32323;
            }
        }

        ul {
            margin-bottom: 24px;



            li {

                margin-bottom: 6px;

                a {
                    color: #0073bb;

                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }

        &__language {
            margin-bottom: 24px;

            h2 {
                color: #d32323;
                font-weight: 500;
                margin-bottom: 6px;
                font-size: 16px;
                line-height: 21px;
                color: #d32323;
                margin-bottom: 12px;
            }
        }

        &__Countries {
            margin-bottom: 24px;

            h2 {
                color: #d32323;
                font-weight: 500;
                margin-bottom: 6px;
                font-size: 16px;
                line-height: 21px;
                color: #d32323;
                margin-bottom: 12px;
            }
        }
    }

    &__image {

        margin-bottom: 18px;
        margin-top: 42px;
        text-align: center;

        // position: absolute;
        // right: 25%;
        // bottom: 0;
        // z-index: 1;
    }

    &__copyright {
        padding-bottom: 18px;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;


        span {
            line-height: 14px;
            font-size: 12px;
        }

        img {
            &:first-of-type {

                width: 38px;
                height: 19px;

            }

            width: 13px;
            height: 19px;
            margin: 0 4px;

        }
    }
}
@media(max-width:576px)
{
    .footer-type1
    {
        display: none;
    }
}