.header-yelp {
    border-bottom: 1px solid #e6e6e6;
    padding: 24px 40px 0;

    &__menu {
        display: flex;
        align-items: center;

        &__nav {
            // width: 581px;
            margin-right: auto;

            ul {
                display: flex;
                align-items: center;

                li {
                    // margin-right: 16px;

                    a {
                        line-height: 40px;
                        font-size: 16px;
                        display: block;
                        text-align: center;
                        padding: 0 16px;
                        border-radius: 4px;
                        color: #2b273c;
                        transition: 0.8s;
                        font-weight: 500;

                        &:hover {
                            background-color: #e0e0e0;

                        }
                    }

                    &:last-child {
                        margin-right: 12px;
                        width: 127px;

                        a {
                            color: #fff;
                            background-color: #f43939;
                            border: 1px solid #f43939;
                            position: relative;

                            &:hover {
                                box-shadow: 0 1px 4px 0 rgba(0, 0, 0, .3);
                            }
                        }
                    }

                    &:nth-child(3) {
                        a {
                            border: 1px solid #bbbac0;
                            width: 113px;
                            // margin-right: 16px;

                            &:hover {
                                background-color: #e0e0e0;
                            }
                        }
                    }
                }
            }
        }

        &__search {
            width: 544px;
            margin-left: 32px;
            background-color: #fff;
            display: flex;
            align-items: center;
            border-radius: 4px;
            box-shadow: 0 2px 18px rgba(0, 0, 0, .15);

            &__right {
                $self: &;
                background-color: #fff;
                padding-right: 10px;
                position: relative;
                border-radius: 4px;
                flex: 0 0 244px;
                max-width: 244px;
                line-height: 43px;


                &--open {
                    border-bottom-right-radius: 0;

                    #{$self}__menu-bottom {
                        display: block;
                    }

                }

                input {
                    border: none;
                    font-size: 16px;
                    height: 22px;
                    color: #F2F2F3;
                }

                &__menu-bottom {
                    background-color: #fff;
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 100%;
                    border-bottom-left-radius: 4px;
                    border-bottom-right-radius: 4px;
                    border-top: 1px solid #e6e6e6;
                    display: none;
                    z-index: 2;

                    ul {
                        padding-right: 10px;
                        z-index: 2;
                        position: relative;

                        li {
                            a {
                                i {}
                            }
                        }
                    }
                }
            }

            &__left {
                flex: 0 0 244px;
                max-width: 244px;
                line-height: 43px;
                background-color: #fff;
                border-radius: 4px;
                $self: &;

                position: relative;

                &--open {
                    border-bottom-right-radius: 0;

                    #{$self}__menu-bottom {
                        display: block;
                    }

                }

                input {
                    border: none;
                    font-size: 16px;
                    height: 22px;
                    padding-right: 10px;
                    border-right: 1px solid #e6e6e6;
                }

                &__menu-bottom {
                    background-color: #fff;
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 100%;
                    border-bottom-left-radius: 4px;
                    border-bottom-right-radius: 4px;
                    border-top: 1px solid #e6e6e6;
                    display: none;
                    z-index: 2;

                    ul {
                        padding-right: 10px;
                        z-index: 2;
                        position: relative;

                        li {
                            a {
                                i {}
                            }
                        }
                    }
                }
            }

            &__button {
                flex: 0 0 56px;
                max-width: 56px;
                background-color: #d32323;
                border-bottom-left-radius: 4px;
                border-top-left-radius: 4px;
                line-height: 43px;


                button {
                    padding: 0;
                    border: none;
                    width: 100%;
                    background-color: rgb(244, 57, 57);
                    border-bottom-left-radius: 4px;
                    border-top-left-radius: 4px;

                    svg {
                        color: red;
                        font-size: 24px;
                        fill: #fff;
                    }
                }
            }

        }


        &__logo {

            display: flex;
            align-items: center;
            margin-left: 32px;
            flex: 0 0 80px;
            max-width: 80px;


            a {
                display: block;

                img {
                    width: 80px;
                    height: 40px;

                }
            }

        }
    }

    &__menu-bottom {
        text-align: center;
        display: flex;
        margin-right: 112px;

        >ul {
            display: flex;
            align-items: center;



            >li {
                padding: 0 12px;
                position: relative;
                border-bottom: 4px solid transparent;

                &:hover {
                    border-bottom: 4px solid #e83636;

                }

                >a {
                    color: #2b273c;
                    font-weight: 500;
                    line-height: 60px;
                    font-size: 14px;
                    // padding-bottom: 16px;
                    display: flex;
                    align-items: center;

                    svg {
                        padding-left: 5px;
                    }
                }

                ul {
                    columns: 2;
                    visibility: hidden;
                    opacity: 0;
                    background-color: #fff;
                    position: absolute;
                    right: 0;
                    top: 100%;
                    margin-top: 4px;
                    transition: 0.5s;
                    z-index: 2;
                    border-radius: 3px;
                    border: 1px solid #e6e6e6;
                    box-shadow: 0 2px 8px rgba(0, 0, 0, .2);
                    width: 350px;
                    padding: 25px;


                    li {
                        line-height: 40px;
                        text-align: right;

                        &:hover {
                            background-color: #e0e0e0;

                            border-radius: 4px;
                        }

                        a {
                            color: #2b273c;

                            svg {
                                margin-left: 5px;
                            }


                        }
                    }
                }

                &:hover {
                    >ul {
                        opacity: 1;
                        visibility: visible
                    }
                }


            }
        }
    }

    &__menu-mobail {
        display: none;

        &__top {
            display: flex;
            align-items: center;
            padding-bottom: 10px;

            &__icon {
                margin-left: 25px;
                position: relative;

                svg {
                    fill: rgba(153, 153, 153, 1);
                    width: 24px;
                    height: 24px
                }


            }

            &__icon-search {

                svg {
                    fill: rgba(153, 153, 153, 1);
                }
            }

            &__logo {
                margin: 0 auto;

                img {}

            }

            &__open-app {
                margin-right: auto;

                a {
                    border: 1px solid #91151c;
                    background-color: #bc1d25;
                    border-radius: 3px;
                    display: flex;
                    align-items: center;
                    color: #fff;
                    padding: 0 10px;
                    line-height: 32px;

                    svg {
                        fill: #fff;
                    }
                }
            }
        }

        &__bottom {
            margin: 0 -10px;


            >ul {
                display: flex;
                width: 100%;
                align-items: center;
                position: relative;


                >li {
                    width: 33.33%;
                    flex: 0 0 33.33%;
                    border: 1px solid #e6e6e6;


                    &:first-child {
                        border-right: none;
                    }

                    &:last-child {
                        border-left: none;
                    }

                    a {
                        display: flex;
                        align-items: center;
                        font-weight: 500;
                        padding: 0 12px;
                        background-color: #fafafa;
                        font-size: 12px;
                        color: black;
                        line-height: 40px;

                        &:hover {
                            color: #0073bb;
                            fill: #0073bb;
                        }



                        svg {
                            margin-left: 5px;
                        }
                    }

                    >ul {
                        columns: 2;
                        visibility: hidden;
                        opacity: 0;
                        background-color: #fff;
                        position: absolute;
                        right: 0;
                        width: 100%;
                        top: 100%;
                        z-index: 2;

                        >li {
                            >a {
                                background-color: #fff;
                                font-size: 12px;
                                color: black;
                                line-height: 40px;
                            }
                        }

                    }


                    &:hover {
                        >ul {
                            opacity: 1;
                            visibility: visible;
                        }
                    }

                }
            }
        }

    }
}


