.page-intro {
	position: relative;

	&__background {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		z-index: 1;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: top center;

		}
	}

	.container {
		max-width: 1174px;
		z-index: 2;
	}

	&__box {
		height: 780px;
		display: flex;
		align-items: center;

		&__text {
			background: #fff;
			box-shadow: 0 0 18px rgba(0, 0, 0, .15);
			border-radius: 8px;
			padding: 48px 68px;
			z-index: 9;
			width: 556px;

			&__title {
				margin-bottom: 24px;

				h1 {
					font-size: 35px;
					line-height: 54px;
					font-weight: 600;
					color: #2b273c;

				}
			}

			&__desc {
				margin-bottom: 32px;

				p {
					font-weight: 400;
					color: #757280;
					font-size: 20px;
					line-height: 30px;
					margin-bottom: 0;
				}
			}

			&__button {
				a {
					color: #fff;
					display: flex;
					background: #f43939;
					border: 1px solid #f43939;
					transition: all .8s;
					border-radius: 4px;
					padding: 0 16px;
					line-height: 46px;
					display: inline-flex;
					font-size: 16px;
					width: unset;
					justify-content: center;

					&:hover {
						box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.3);
					}
				}
			}
		}
	}
}

@media(max-width:1024px) {
	.page-intro {
		&__box {
			align-items: flex-end;
			height: 380px;

			&__text {

				margin-bottom: -150px;
				width: 100%;

			}
		}
	}
}
