    }
    &__list
    {
        margin-bottom: 12px;
        ul
        {
            display:flex;
            justify-content: center;
            li
            {
                margin-left: 12px;
                &:last-child
                {
                    margin-left: 0;
                }
                a
                {
                    font-size: 14px;
                    color: #0073bb;
                    &:hover
                    {
                        text-decoration: underline;
                    }

                }
            }
        }
    }
    &__add-work
    {
        text-align: center;
        a

        {
            font-size: 14px;
            color: #0073bb;
            &:hover
            {
                text-decoration: underline;
            }

        }
    }
    &__copyright {
        margin-bottom: 10px;
        text-align: center;
        margin-top: 24px;
        padding-right: 15px;

        span {
            color: #757280;
            font-size: 14px;
            line-height: 18px;
        }
        img
        {
            width: 13px;
            height: 19px;
            &.img
            {
                width: 38px;
            height: 19px;

            }
        }
       
    }
}
@media(max-width:576px)
{
    .footer-mobile2
    {
        display:block;
    }
}

@media(max-width:480px)
{
    .footer-mobile2
    {
        &__language
        {
            flex: 0 0 100%;
            max-width: 100%;

        }
        &__country
        {
            flex: 0 0 100%;
            max-width: 100%;

        }
        &__list
        {

            ul
            {
                li
                {
                    margin-left:5px;
                    a
                    {
                        font-size: 12px;
                        color: #0073bb;
                        &:hover
                        {
                            text-decoration: underline;
                        }
    
                    }
                }
            }
        }
    }
}