.header-type3 {
	box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .08);
	background-color: #fff;
	position: fixed;
	z-index: 999;
	left: 0;
	top: 0;
	right: 0;

	.container,
	.container-fluid {
		display: flex;
		align-items: center;
		padding: 0 32px;
	}

	&__logo {
		display: flex;
		align-items: center;

		img {
			width: 80px;
			height: 40px;

		}

		span {
			font-size: 20px;
			line-height: 26px;
			font-weight: 400;
			color: #2b273c;
			margin-right: 12px;


		}
	}

	&__nav {
		margin-right: 20px;

		>ul {
			display: flex;
			align-items: center;

			>li {
				position: relative;

				>a {
					color: #2b273c;
					font-size: 14px;
					display: flex;
					padding: 0 16px;
					align-items: center;
					line-height: 69px;
					border-bottom: 4px solid transparent;

					svg {
						fill: rgba(43, 39, 60, 1);
						margin-right: 4px;
					}
				}

				>ul {
					position: absolute;
					top: 100%;
					right: 0;
					background-color: #fff;
					border-radius: 4px 0 4px 4px;
					visibility: hidden;
					opacity: 0;
					transition: 0.5s;
					z-index: 2;
					width: 200px;
					padding: 16px 15px;
					box-shadow: 0 0 18px rgba(0, 0, 0, .15);

					>li {
						>a {
							color: #2b273c;
							font-size: 14px;
							line-height: 40px;
							padding: 0 15px;
							display: flex;
							border-radius: 4px;

							&:hover {
								background-color: #efefef;
							}

						}
					}
				}

				&.active {
					>a {
						border-color: #f43939;
					}
				}

				&:hover {
					>a {
						border-color: #f43939;
					}

					>ul {
						visibility: visible;
						opacity: 1;
					}
				}
			}
		}
	}

	&__phone {
		display: flex;
		align-items: center;
		margin-right: auto;
		flex-direction: row-reverse;

		a {
			color: #2b273c;
			font-weight: 600;
			font-size: 14px;
		}

		svg {
			margin-left: 10px;
		}
	}

	&__button {
		ul {
			display: flex;
			align-items: center;

			li {

				a {
					transition: all .8s;
					border-radius: 4px;
					padding: 0 16px;
					line-height: 38px;
					display: flex;
					font-size: 16px;
				}

				&:first-child {
					margin-right: 24px;

					a {
						color: #2b273c;
						border: 1px solid #bbbac0;


						&:hover {
							background-color: rgba(0, 0, 0, .12);
						}

					}
				}

				&:last-child {
					margin-right: 16px;

					a {
						background: #f43939;
						border: 1px solid #f43939;
						color: #fff;

						&:hover {
							box-shadow: 0 1px 4px 0 rgba(0, 0, 0, .3);
						}

					}
				}
			}
		}
	}

}



@media(max-width:1250px) {
	.header-type3 {
		display: none;
	}
}
