.header-business
{
    background: #d32323;
    padding: 13px 0;
    background-image: url("../img/header-stars.png");
    background-position:center;
    &--national
    {
        padding: 16px 0;
    }
    .container
    {
        display: flex;
        align-items: center;
    }
    &__logo
    {
        display: flex;
        align-items: center;
        img
        {
            margin-left: 12px;
        }
        span
        {
            color: white;
            font-size: 18px;
            font-weight: 600;
            line-height: 23px;
        }
    }
    &__nav
    {
        margin-right:auto;
        ul
        {
            display: flex;
            align-items: center;
            margin-right:auto;
            li
            {
                a
                {
                    color: #fff;
                    font-size: 13px;
                    line-height: 35px;
                    padding: 0 18px;
                    display: flex;
                    border-radius: 4px;
                }
                &:last-child
                {
                    a
                    {
                        margin-right: 6px;
                        background: #bd1f1f;
                    }
                }
                &:hover
                {
                    a
                    {
                        background: #a71c1c;
                        
                    }
                }
            }
        }
    }
    &__link
    {
        margin-right: auto;
        a
        {
            color: #fff;
            display: flex;
            align-items: center;
            svg
            {
                margin-left: 10px;
                fill: #fff;
            }
        }
    }
    &__mobile-toggle
    {
        display: none;
        margin-right: auto;
        svg
        {
            fill: #fff;
        }
    }
}

@media(max-width:768px)
{
    .header-business
    {
        &__link
        {
            display: none;
        }
        &__mobile-toggle
        {
            display: block;
        }
    }
}

@media(max-width:480px)
{
    .header-business
    {
        padding: 7px 0;
        &__nav
        {
            ul
            {
                li
                {
                    &:last-child
                    {
                        display:none;
                    }
                }
            }
        }
        &__logo
        {
            img
            {
                height: 35px;
            }
            span
            {
                font-size: 14px;
            }
        }
    }
}

