.page-header
{
    &__wrapper
    {
        border-bottom: 1px solid #e6e6e6;
        margin-bottom: 11px;
        padding: 18px 0;
        ul
        {
            display: flex;
            align-items: center;
            margin-bottom: 5px;
            li
            {

                a
                {
                    color: #0073bb;
                    font-weight: 600;
                    font-size: 13px;
                }
                svg
                {
                    transform: rotateY(180deg);
                    fill: #999;
                }
            }
        }
        h2
        {
            color: #d32323;
            font-size: 18px;
            font-weight: 600;
        }
    }
}