.page-alert {
	.container {
		max-width: 1174px;
	}

	&__wrapper {
		box-shadow: 0 0 10px rgba(0, 0, 0, .1);
		padding: 48px 68px;
        margin-bottom: 96px;
        border-radius: 8px;
	}

	&__title {
		margin-bottom: 32px;
		text-align: center;

		h2 {
			font-size: 24px;
            line-height: 36px;
            font-weight: 600;
			color: #2b273c;
		}
	}

	&__button {
		text-align: center;
		display: flex;
		justify-content: center;

		a {
			color: #fff;
			display: flex;
			background: #f43939;
			border: none;
			transition: all .8s;
			border-radius: 4px;
			padding: 0 16px;
			line-height: 40px;
			display: flex;
			font-size: 16px;
			width: fit-content;
			justify-content: center;

			&:hover {
				box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.3);
			}
		}
	}
}
