.page-blog {
    margin-bottom: 64px;
    padding-bottom: 74px;
    padding-top: 48px;
    background-color: #f4f4f4;

	.container {
		max-width: 1174px;
    }
    &--white
    {
        background-color: transparent;
    }
    &__title
    {
        margin-bottom: 48px;
        text-align: center;
        h3
        {
            font-size: 28px;
            line-height: 36px;
            color: #2b273c;
        }
    }

    &__item
    {
        margin-bottom: 30px;
        &__image
        {
            margin-bottom: 15px;
            img
            {
                border-radius: 4px;
                width: 100%;
                height: 100%;
            }
        }
        &__content
        {
            h2
            {
                font-size: 15px;
                font-weight: 600;
                color: #2b273c;
                margin-bottom: 24px;
            }
            a
            {
                color: #00838f;
                font-size: 15px;
                font-weight: 600;
                &:hover
                {
                    text-decoration: underline;
                }
            }
        }
	}
}
