.popular-events
{
    &__title
    {
        margin-bottom: 18px;
        h2

        {
            color: #d32323;
            font-size: 21px;
            line-height: 26px;

        }
    }
    &__nav
    {
        display: flex;
        align-items: center;
        margin-bottom:20px;
        span
        {
            font-weight: 500;
            color: #333;
            font-size: 13px;
            // margin-left: 6px;

        }
        ul
        {
            display: flex;
            align-items: center;

            li
            {
                padding:0  6px;
                border-left: 1px solid  #0073bb ;
                &:last-child
                {
                    border-left:none;
                }
                a
                {
                    color: #0073bb;
                 font-size: 13px;

                    &:hover{
                        text-decoration: underline;
                    }

                }
            }
        }
    }
    &__item
    {
        background: #fff;
        border: 1px solid #e6e6e6;
        border-radius: 4px;
        margin-bottom: 30px;
        height:calc(100% - 30px);
        position: relative;
        &__see-all
        {
            position: absolute;
            top: 50%;
            right: 50%;
            transform: translateX(50%);
            width: 100%;
            text-align: center;
            display: flex;
            a
            {
                display: flex;
                flex-direction: column;
                color:#fff;
                font-size: 16px;
                text-align: center;
                justify-content: center;
                flex: 0 0 100%;
                &:hover{
                    text-decoration: underline;
                }
                svg
                {
                    fill: #fff;
                    margin: 0 auto;

                }
            }
        }
        &__image
        {
            border-bottom: 1px solid #e6e6e6;
            
            img
            {
                border-radius: 4px 4px 0 0;
                width: 298px;
                height:180px;
                object-fit: cover;

            }
            &--fit
            {
                height:100%;
                border-bottom: none;
                position: relative;
                &:before
                {
                    position: absolute;
                    content: "";
                    bottom:0;
                    top:0;
                    left: 0;
                    right: 0;
                    background: linear-gradient(to bottom,rgba(0,0,0,0), #000);

                }
               img
               {
                height: 100%;
               }

            }
        }
        &__text
        {          
            &__top
            {
                padding: 12px;
                border-bottom: 1px solid #e6e6e6;
                margin-bottom: 12px;
                &__title
                {
                    margin-bottom: 6px;
                    height: 42px;
                    overflow: hidden;
                    a
                    {
                        color: #0073bb;
                        font-size:14px;
                        font-weight: 500;
                        line-height: 21px;
                        &:hover{
                            text-decoration: underline;
                        }
                    }
                }
                &__date
                {
                    height: 24px;
                    overflow: hidden;
                    margin-bottom: 6px;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;

                    svg
                    {
                        fill: #666;
                    }
                    span
                    {
                        color: #333;
                        line-height: 24px;
                    }
                }
                &__location
                {
                    margin-bottom: 6px;
                    white-space: nowrap;
                   text-overflow: ellipsis;
                   overflow: hidden;
                    svg
                    {
                        fill: #666;
                    }

                    span
                    {
                        color: #333;
                        font-size: 14px;
                    }
                    a
                    {
                        color: #0073bb;
                        &:hover
                        {
                            text-decoration:underline;
                        }
                    }
                }
                &__desc
                {
                   
                    p
                    {
                        text-align: justify;
                        color: #333;
                        margin-bottom: 0;
                        line-height: 18px;
                        height: 36px;
                        overflow: hidden;
                        margin-bottom: 12px;
                    }
                }
            }
            &__bottom
            {
                padding:0 12px 12px 12px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                a
                {
                    color: #0073bb;
                    font-size: 14px;
                    &:hover
                    {
                        text-decoration: underline;
                    }

                }
                span
                {
                    color: #999;

                }
            }
        }
    }
}

@media(max-width:767px)
{
    .popular-events
    {
        &__item
        {
            &__image
            {
                img
                {
                    width: 100%;
                    object-fit: cover;
                }
                &--fit
                {
                    height: 410px;
                }
            }
        }
    }
}

@media(max-width:576px)
{
    .popular-events
    {
        &__item
        {
            &__image
            {
                &--fit
            {
                height:100%;
               img
               {
                   width: 100%;
                   height:250px;
               }

            }
            }
        }
        &__nav
        {
            span
            {
                margin-bottom: 12px;
                font-size: 11px;
            }
            ul
            {
                li
                {
                    margin-bottom: 12px;
                    padding: 0 5px;
                    a
                    {
                        font-size: 11px;
                    }
                }

            }
        }
       
    }
}

@media(max-width:480px)
{
    .popular-events
    {
        &__nav
        {
            flex-direction: column;
            align-items: end;
            span
            {
                margin-bottom: 12px;

            }
            ul
            {
                flex-direction: column;
                align-items: end;
                li
                {
                    margin-bottom: 12px;
                    border-left: none;
                }

            }
        }
        &__item
        {
            &__image
            {
                img
                {
                    width: 100%;
                }
            }
        }
    }
}