.row
{
    &[data-space="0"]
    {
        margin-right: 0;
        margin-left: 0;
        >[class*="col-"]
        {
            padding-right: 0;
            padding-left: 0;
        }
    }
    &[data-space="10"]
    {
        margin-right: -5px;
        margin-left: -5px;
        >[class*="col-"]
        {
            padding-right: 5px;
            padding-left: 5px;
        }
    }
    
    &[data-space="15"]
    {
        margin-right: -7.5px;
        margin-left: -7.5px;
        >[class*="col-"]
        {
            padding-right: 7.5px;
            padding-left: 7.5px;
        }
    }
    &[data-space="20"]
    {
        margin-right: -10px;
        margin-left: -10px;
        >[class*="col-"]
        {
            padding-right: 10px;
            padding-left: 10px;
        }
    }
    &[data-space="24"]
    {
        margin-right: -12px;
        margin-left: -12px;
        >[class*="col-"]
        {
            padding-right: 12px;
            padding-left: 12px;
        }
    }
    &[data-space="50"]
    {
        margin-right: -25px;
        margin-left: -25px;
        >[class*="col-"]
        {
            padding-right: 25px;
            padding-left: 25px;
        }
    }
    &[data-space="60"]
    {
        margin-right: -30px;
        margin-left: -30px;
        >[class*="col-"]
        {
            padding-right: 30px;
            padding-left: 30px;
        }
    }
}

.col-x5,
.col-xl-x5,
.col-lg-x5,
.col-md-x5,
.col-sm-x5
{
    position: relative;
    width: 100%;
    min-height: 1px;
    padding-right: 15px;
    padding-left: 15px;
}