.start-business
{
    margin-top: 15px;
    margin-bottom: 36px;
    &__form
    {
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        margin-bottom: 123px;
        &__image
        {
            margin-bottom: 6px;
            img
            {
                width:191px;
                height:154px;
            }
        }
        &__title
        {
            h1
            {
                color: #333;
                line-height: 42px;
                font-size: 20px;
                font-weight: 600;
                margin-bottom:10px;

            }
            p

            {
                margin-bottom: 24px;
                line-height: 17px;
                color: #333;

            }
        }
    &__input
    {
        width: 382px;
        line-height: 18px;
        margin: auto;
        &__country
        {
            display: none;
            &__lable
            {
                margin-bottom: 6px;
                text-align: right;
                span
                {
                    color: #333;
                    font-size: 14px;
                    line-height: 17px;
                    font-weight: 500;
                }
            }
            &__select
            {
                margin-bottom: 18px;
                select
                {
                    width: 100%;
                    text-align: right;
                    padding: 0 9px;
                    background: #fff;
                    border: 1px solid #999;
                    border-radius: 3px;
                    font-size: 14px;
                    line-height: 30px;
                    height: 30px;
                    &:focus
                    {
                        box-shadow: 0 0 4px rgba(0,115,187,0.3);
                        border-color: #0073bb;
                    }
                }
            }
        }
        &__input-form-phone
        {
            &__lable
            {
                display: flex;
                justify-content: space-between;
                margin-bottom: 6px;
                span
                {
                    color: #333;
                    font-size: 14px;
                    line-height: 17px;
                    &:first-child
                    {
                        font-weight: 500;
                    }
                }
            }
            &__input
            {
                margin-bottom: 18px;
                input
                {
                    text-align: left;
                    padding: 5px 9px;
                    background: #fff;
                    border: 1px solid #999;
                    border-radius: 3px;
                    font-size: 14px;
                    width: 100%;
                    &:focus
                    {
                        box-shadow: 0 0 4px rgba(0,115,187,0.3);
                        border-color: #0073bb;
                    }
                }
            }
        }
        &__input-form-name
        {
            &__lable
            {
                margin-bottom: 6px;
                text-align: right;
                span
                {
                 
                    color: #333;
                    font-size: 14px;
                    line-height: 17px;
                    font-weight: 500;
                }
            }
            &__input
            {
                margin-bottom: 18px;
                input
                {
                    width: 100%;
                    text-align: right;
                    padding: 5px 9px;
                    background: #fff;
                    border: 1px solid #999;
                    border-radius: 3px;
                    font-size: 14px;
                    &:focus
                    {
                        box-shadow: 0 0 4px rgba(0,115,187,0.3);
                        border-color: #0073bb;
                    }
                }
            }
        }
        &__button
        {
            text-align: right;
            button
            {
                color: #fff;
                background: #d32323;
                border: 1px solid #d32323;
                transition: all 0.6s;
                line-height: 34px;
                border-radius: 3px;
                font-size: 14px;
                min-width: 100px;
                height: 36px;
                padding: 0;
                &:hover
                {
                    background-color: #dc4f4f;
                    border: 1px solid #dc4f4f;
                }
            }
        }
    }
    }
}

@media(max-width:576px)
{
    .start-business
    {
        &__form
        {
            margin-bottom: 0;
          &__title
          {
              h1
              {
                  font-size: 24px;
              }
          }
          &__input
          {
              width:100%;
              &__input-form-phone
              {
                  input
                  {
                      width: 100%;
                  }
              }
              &__input-form-name
              {
                  input
                  {
                      width: 100%;
                  }
              }
          }
        }
    }
}