.page-intro1 {
	position: relative;
	padding: 40px 0 0;

	.container {
		max-width: 1174px;
		z-index: 2;
	}

	&__image {
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&__text {
		&__title {
			margin-bottom: 24px;

			h1 {
				font-size: 35px;
				line-height: 54px;
				font-weight: 600;
				color: #2b273c;

			}
		}

		&__desc {
			margin-bottom: 32px;

			p {
				font-weight: 400;
				color: #757280;
				font-size: 20px;
				line-height: 30px;
				margin-bottom: 0;
			}

			ul {
				font-size: 15px;
				line-height: 26px;
				color: #2b273c;
				margin-bottom: 30px;

				li {
					margin-bottom: 8px;
				}

				svg {
					margin-left: 10px;
				}
			}

			span {
				color: #2b273c;
				font-size: 15px;

				svg {
					margin-left: 10px;
					fill: #00a71b;
				}

				b {
					font-weight: 600;
					margin-left: 10px;
				}

				a {
					color: #9997a1;
					text-decoration: underline;
					margin-right: 10px;
				}
			}
		}

		&__button {
			a {
				color: #fff;
				display: flex;
				background: #f43939;
				transition: all .8s;
				border-radius: 4px;
				padding: 0 16px;
				line-height: 42px;
				display: inline-flex;
				font-size: 16px;
				width: unset;
				justify-content: center;

				&:hover {
					box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.3);
				}
			}
		}
	}
	&__center-text {
		text-align: center;
		padding: 150px 0 100px;
		&__title {
			margin-bottom: 24px;

			h1 {
				font-size: 35px;
