				}
			}

			span {
				color: #2b273c;
				font-size: 15px;

				svg {
					margin-left: 10px;
					fill: #00a71b;
				}

				b {
					font-weight: 600;
					margin-left: 10px;
				}

				a {
					color: #9997a1;
					text-decoration: underline;
					margin-right: 10px;
				}
			}
		}

		&__button {
			a {
				color: #fff;
