body {
	text-align: right;
	font-family: 'yekan';
	font-size: 12px;
	direction: rtl;
	overflow-x: hidden;

	&.header-fix {
		padding-top: 153px;

		&.header-menu-hide {
			padding-top: 89px;

			.category-content__filter-mobail {
				top: 89px;
			}

			.category-content__map {
				top: 89px;
			}

			.category-content__item-filter {
				top: 110px;

				&--active {
					top: 140px;
				}
			}
		}

		.header-type1 {
			position: fixed;
			left: 0;
			right: 0;
			top: 0;
			z-index: 100;
        }
        
    }
    &.header-bussiness-fix
    {
        padding-top: 73px;
	}
	&.header-adpartners-fix
	{
        padding-top: 87px;
	}
	
}

@media(max-width: 750px) {
	body {
		&.header-fix {
			padding-top: 148px;

			.category-content__filter-mobail {
				top: 98px
			}

			.category-content__map {
				top: 148px;
			}

			.category-content__item-filter {
				top: 149px;

				&--active {
					top: 149px;
				}
			}

			&.header-menu-hide {
				padding-top: 0;

				.category-content__filter-mobail {
					top: 98px
				}

				.category-content__map {
					top: 98px;
				}

				.category-content__item-filter {
					top: 149px;

					&--active {
						top: 149px;
					}
				}
			}
		}
		&.header-home-fix
		{
			padding-top: 98px;
		}
	}
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0
}

img {
	max-width: 100%;
}

ul,
ol {
	margin: 0;
	padding: 0;

	li {
		list-style: none
	}
}

a,
a:hover,
a:active,
a:visited {
	text-decoration: none;
	color: #0073bb;
}

*:focus {
	outline: none
}

.container {
	max-width: 990px;
}

.row {
	&[data-space="0"] {
		margin-right: 0;
		margin-left: 0;

		>[class*="col-"] {
			padding-right: 0;
			padding-left: 0;

			&.right-sidebar-padding {
				padding-right: 30px;
			}
		}
	}

	&[data-space="10"] {
		margin-right: -5px;
		margin-left: -5px;

		>[class*="col-"] {
			padding-right: 5px;
			padding-left: 5px;
		}
	}

	&[data-space="12"] {
		margin-right: -6px;
		margin-left: -6px;

		>[class*="col-"] {
			padding-right: 6px;
			padding-left: 6px;
		}
	}

	&[data-space="15"] {
		margin-right: -7.5px;
		margin-left: -7.5px;

		>[class*="col-"] {
			padding-right: 7.5px;
			padding-left: 7.5px;
		}
	}

	&[data-space="20"] {
		margin-right: -10px;
		margin-left: -10px;

		>[class*="col-"] {
			padding-right: 10px;
			padding-left: 10px;
		}
	}

}

.form-control {
	height: 30px;
	padding: 0 15px;
	font-size: 14px;
}

.bootstrap-select .dropdown-menu {

	ul {
		li {
			line-height: 16px;
			margin-bottom: 0;
		}
	}
}
