.footer-type2 {
    padding-top: 36px;
    font-size: 14px;
    line-height: 18px;
    background-color: #f5f5f5;
    margin-top: 48px;

    &__select
    {
        &__item {
            select {
                    
            }
    
            option {
                font-weight: 400;
                font-size: 14px;
                line-height: 20px;
            }
            .dropdown-menu
            {
               width: auto;
               border-radius: 0;
               box-shadow: 0 2px 8px rgba(0,0,0,0.2);
            }
            .form-control
            {
                background-color:transparent;
            }
    
            .bootstrap-select:not(.input-group-btn),
            .bootstrap-select[class*=col-] {
                display: flex;
                align-items: center;
    
            }
    
    
            .btn {
                padding: 0;
            }
    
            .bootstrap-select .dropdown-toggle .filter-option {
                padding-left: 10px;
            }
    
    
            .bootstrap-select .dropdown-menu {
               
                ul
                {
                    li
                    {
                        line-height:16px;
                        margin-bottom: 0;
                    }
                }
            }
    
            .bootstrap-select>.dropdown-toggle {
                background-color: transparent;
                border: transparent;
                width: auto;
                margin-bottom: 24px;
            }
    
            .bootstrap-select>.dropdown-toggle:hover .filter-option-inner-inner {
                text-decoration: underline;
                text-align:right;
    
            }
    
            .btn-light:not(:disabled):not(.disabled).active,
            .btn-light:not(:disabled):not(.disabled):active,
            .show>.btn-light.dropdown-toggle {
                color: #757280;
                background-color: transparent;
                border-color: transparent;
                border: none;
                outline: none;
                box-shadow: none;
    
            }
            .btn-light
            {
                color: #757280;
            }
    
            .bootstrap-select .dropdown-toggle:focus,
            .bootstrap-select>select.mobile-device:focus+.dropdown-toggle {
                outline: none !important;
                box-shadow: none;
            }
    
            .filter-option-inner-inner {
                text-align: right;
                font-weight: 400;
                font-size: 14px;
                line-height: 20px;
            }
    
            .dropdown-menu {
                .inner {
                    .show {
                        text-align: right;
                    }
                }
    
                .selected {
                    &.active {
                        background-color:  #757280;;
                    }
                }
    
            }
            &__title {
                margin: 15px 0;
    
                h2 {
                    font-weight: 500;
                    margin-bottom: 6px;
                    font-size: 20px;
                    line-height: 22px;
                    color: #2b273c;
                }
            }
    
            ul {
    
    
                li {
    
                    margin-bottom: 6px;
    
                    a {
                        font-size: 14px;
                        line-height: 20px;
                        color: #757280;
                    }
                }
            }
        }
    }
    &__item {
        select {
                
        }

        option {
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
        }
        .dropdown-menu
        {
           width: auto;
           border-radius: 0;
           box-shadow: 0 2px 8px rgba(0,0,0,0.2);
        }
        .form-control
        {
            background-color:transparent;
        }

        .bootstrap-select:not(.input-group-btn),
        .bootstrap-select[class*=col-] {
            display: flex;
            align-items: center;

        }


        .btn {
            padding: 0;
        }

        .bootstrap-select .dropdown-toggle .filter-option {
            padding-left: 10px;
        }


        .bootstrap-select .dropdown-menu {
           
            ul
            {
                li
                {
                    line-height:16px;
                    margin-bottom: 0;
                }
            }
        }

        .bootstrap-select>.dropdown-toggle {
            background-color: transparent;
            border: transparent;
            width: auto;
            margin-bottom: 24px;
        }

        .bootstrap-select>.dropdown-toggle:hover .filter-option-inner-inner {
            text-decoration: underline;
            text-align:right;

        }

        .btn-light:not(:disabled):not(.disabled).active,
        .btn-light:not(:disabled):not(.disabled):active,
        .show>.btn-light.dropdown-toggle {
            color: #757280;
            background-color: transparent;
            border-color: transparent;
            border: none;
            outline: none;
            box-shadow: none;

        }
        .btn-light
        {
            color: #757280;
        }

        .bootstrap-select .dropdown-toggle:focus,
        .bootstrap-select>select.mobile-device:focus+.dropdown-toggle {
            outline: none !important;
            box-shadow: none;
        }

        .filter-option-inner-inner {
            text-align: right;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
        }

        .dropdown-menu {
            .inner {
                .show {
                    text-align: right;
                }
            }

            .selected {
                &.active {
                    background-color:  #757280;;
                }
            }

        }
        &__title {
            margin: 15px 0;

            h2 {
                font-weight: 500;
                margin-bottom: 6px;
                font-size: 20px;
                line-height: 22px;
                color: #2b273c;
            }
        }

        ul {


            li {

                margin-bottom: 6px;

                a {
                    font-size: 14px;
                    line-height: 20px;
                    color: #757280;
                }
            }
        }
    }

    &__copyright {
        margin-bottom: 10px;
        text-align: center;
        margin-top: 24px;
        padding-right: 15px;

        span {
            color: #757280;
            font-size: 12px;
            line-height: 18px;
        }
    }
}




