.adpartenrs-services
{
    position: relative;
    &__wrapper
    {
        padding: 20px 15px 50px;
        border-bottom: 1px solid #d4d4d4;
    }
    &__item
    {
        box-shadow: 0 5px 10px 0 rgba(0,0,0,0.2);
        height: 200px;
        position: relative;
        transition: 0.3s;
        margin-bottom: 30px;
        background-color: #fff;
        &__title
        {
            height: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color:#eee;
            span
            {
                display: block;
                text-align: center;
                line-height: 50px;
                font-size: 16px;
            }
        }
        &__image
        {
            height: 150px;
            display: flex;
            align-items: center;
            justify-content: center;
            img
            {
                max-height: 105px;
            }
        }
        
        a
        {
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            z-index: 5;
        }
        &:hover
        {
            box-shadow: 0 10px 20px 0 rgba(0,0,0,0.2);
            span
            {
                text-decoration: underline;
            }
        }
    }
}