.footer-type5
{
    background-color: #fcfcfc;
    margin-bottom: 48px;
    .container {
		max-width: 1174px;
	}
    &__top
    {
        padding: 96px 40px;
        border-bottom: 1px solid #eeeeef;
        &__item
        {
            margin-bottom: 32px;
            &__title
            {
                p
                {
                    font-size: 16px;
                    line-height: 22px;
                    color: #2b273c;
                    margin-bottom: 16px;
                    font-weight: 600;
                }
                ul
                {
                    li
                    {
                        a
                        {
                            color: #757280;                   
                            font-size: 14px;
                            line-height: 25px;
                            &:hover
                            {
                                text-decoration: underline;
                            }
                        }
                    }
                }
            }
        }
    }
    &__copyright {
       padding-top: 48px;
        text-align: center;
        span {
            font-size: 12px;
            line-height: 18px;
            color: #757280;
        }

        img {
            &:first-of-type {

                width: 38px;
                height: 19px;

            }

            width: 13px;
            height: 19px;
            margin: 0 4px;

        }
    }
}
