.page-feature {
	margin-bottom: 64px;

	.container {
		max-width: 1174px;
	}

	&__title {
        text-align: center;
        margin-bottom: 30px;
        span
        {
            color: #9997a1;
            display: block;
            margin-bottom: 30px;
            line-height: 25px;
        }
        h3
        {
            color: #2b273c;
            margin-bottom: 30px;
            line-height: 25px;
        }
        p
        {
            font-size: 13px;
            color: #2b273c;
            line-height: 25px;
        }
	}

	&__text {
        margin-bottom: 25px;
        font-size: 16px;
        line-height: 22px;
        color: #2b273c;

		span {

			font-weight: 600;
		}
		p {
            font-size: 13px;
		}
	}
}
