.header-mobile5
{
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.08);
    background-color: #fff;
    display: none;
    padding: 16px 0;
    position: fixed;
	z-index: 999;
	left: 0;
	top: 0;
	right: 0;
    

    .container
    {
       max-width:1406px ;
       display: flex;
       align-items: center;
    }
    &__menu-mobile
    {
        margin:0 8px;
    }
    &__logo
    {
        display: flex;
        align-items: center;
        img
        {
            width: 80px;
            height: 40px;

        }
        span
        {
            font-size: 20px;
            line-height: 26px;
            font-weight: 400;
            color: #2b273c;
            margin-right: 12px;


        }
    }
  
    &__button
    {
        margin-right: auto;
      ul
      {
          display: flex;
          align-items: center;
          li
          {
              
              a
              {
                transition: all .8s;
                border-radius: 4px;
                padding: 0 16px;
                line-height: 38px;
                display: flex;
                font-size: 16px;
              }
            &:first-child
            {
                margin-right: 24px;
                display: none;
               
                a
                {
                    color: #2b273c;
                    border: 1px solid #bbbac0;
                   
                   
                    &:hover
                    {
                        background-color: rgba(0,0,0,.12);
                    }

                }
            }
            &:nth-child(2)
            {
                margin-right: 16px;
              
                a
                {
                    background: #f43939;
                    border: 1px solid #f43939;
                    color: #fff;
                    &:hover
                    {
                        box-shadow: 0 1px 4px 0 rgba(0,0,0,.3);
                    }

                }
                
            }
            &:last-child
            {
                margin-right: 16px;
                display: none;
                a
                {
                    background: #f43939;
                    border: 1px solid #f43939;
                    color: #fff;
                    &:hover
                    {
                        box-shadow: 0 1px 4px 0 rgba(0,0,0,.3);
                    }

                }
            }
          }
      }
    }

}

@media(max-width:1250px)
{
    .header-mobile5
    {
        display:block;
    }
}


@media(max-width:599px)
{
    .header-mobile5
    {
        &__logo
        {
            img
            {
                width: 64px;
                height: 40px;
            }
            span
            {
                font-size: 12px;
            }
        }
        &__button
        {
            ul
            {
                li
                {
                    &:nth-child(2)
                    {
                        display:none;
                    }
                    &:last-child
                    {
                        display:block;
                    }
                }
            }
        }
    }
}