.page-services
{
    padding: 145px 0 75px;
    .container {
        max-width: 1174px;
    }
    &__title
    {
        text-align: center;
        margin-bottom: 56px;
        h2
        {
            font-size: 28px;
            line-height: 36px;
            color: #2b273c;
            font-weight: 600;
        }
    }
    &__item
    {
        padding: 16px;
        &__image
        {
            margin-bottom: 12px;

        }
        &__title
        {
            margin-bottom: 24px;
            text-align: center;
            h3
            {
                color: #2b273c;
                font-size: 24px;
                line-height: 30px;

            }

        }
        &__desc
        {
            text-align: center;
            p
            {
                color: #757280;
                font-size: 16px;
                line-height: 22px;
            }
        }
    }
}