.footer-events
{
    margin-bottom: 36px;
    &__item
    {
        &--none-title
        {
            margin-top: 31px;
        }
       &__title
       {
       
         margin-bottom:10px;
            h3
            {
             color: #d32323;
             font-size: 16px;
             font-weight: 500;
 
            }
          
       }
       ul
       {
           &.li-img
           {
               li
               {
                   margin-bottom: 12px;
               }
           }
           li
           {
               margin-bottom: 6px;
               display: flex;
               svg
               {
                fill: #0073bb;
                margin-left: 12px;
               }
               a
               {
                   color: #0073bb;
                   &:hover{
                       text-decoration: underline;
                   }

               }
           }
       }
       &__image
       {
           margin-left: 6px;
           img
           {
               width: 30px;
               height: 30px;
               border-radius: 4px;
               max-width: unset;
           }
       }
       &__text
       {
        
           
          p
          {
            font-size: 12px;
            margin-bottom: 0;
              a
              {
                color: #0073bb;
                font-size: 12px;
                margin-left:3px;
                &:hover
                {
                    text-decoration: underline;
                }

              }
              span
              {
                color: #999;

              }
          }
       }
       &__see-all
       {
        a
        {
            color: #0073bb;
            &:hover{
                text-decoration: underline;
            }

        }
       }
    }
}

@media(max-width:576px)
{
    .footer-events
    {
        margin-top: 0;
        &__item
        {
            ul
            {
                margin-bottom: 15px;
            }
        }
    }
}