.header-type2
{
    background: #d32323;
    padding: 12px 0;
    margin-bottom: 15px;
    &__logo
    {
        display:flex;
        align-items: center;
        justify-content: center;
        a
        {
            img
            {
                width: 80px;
                height: 40px;
            }
        }
    }
}

@media(max-width:576px)
{
    .header-type2
    {
        margin-bottom: 15px;
    }
}