.yelp-paris {
    background-color: #f5f5f5;

    &__title {
        text-align: center;
        padding-top: 36px;

        h2 {
            font-weight: 500;
            margin-bottom: 6px;
            font-size: 21px;
            line-height: 26px;
            color: #d32323;

        }

    }

    &__menu {
        text-align: center;


        ul {
            display: flex;
            border-bottom: 1px solid #e6e6e6;
            justify-content: center;

            li {
                display: flex;
                align-items: center;
                padding: 0 11px;
                border-bottom: 3px solid transparent;

                &:hover {
                    border-bottom: 3px solid #d32323;
                }


                a {
                    line-height: 32px;
                    color: #666;

                    svg {
                        fill: #666;
                        margin-left: 6px;
                    }

                }
            }
        }

    }

    &__content {
        margin-top: 30px;

        &__item {
            $self: &;
            border: 1px solid #e6e6e6;
            border-radius: 4px;
            background-color: #fff;
            text-align: center;
            padding-top: 17px;
            padding-bottom: 17px;
            margin-bottom: 42px;
            position: relative;

            &:hover {
                border-color: #0073bb;
                box-shadow: 0 6px 12px rgba(0, 0, 0, .1);

                a {
                    h3 {
                        color: #0073bb;
                    }
                }
            }


            a {
                align-items: center;

                img {
                    height: 72px;
                    width: 72px;
                    margin-bottom: 6px;

                }

                h3 {
                    color: #333;
                    font-weight: 500;
                    margin-bottom: 6px;
                    font-size: 14px;
                    line-height: 17px;
                    margin-bottom: 6px;


                }
                &:after
                {
                    content: '';
                    position: absolute;
                    right: 0;
                    left: 0;
                    top: 0;
                    bottom: 0;
                    z-index: 5;
                }
            }
        }
    }
    &__more
    {
        border-top: 1px solid #e6e6e6;
        padding-top:30px;
        padding-bottom: 36px;
        display: none;
        position: relative;
        &:before {
            content: '';
            border-width: 0 13px 13px 13px;
            border-style: solid;
            border-color: transparent transparent #e6e6e6;
            position: absolute;
            top: -14px;
            left:10%;
        }
        &:after {
            content: '';
            border-width: 0 13px 13px 13px;
            border-style: solid;
            border-color: transparent transparent #f5f5f5;
            position: absolute;
            top: -13px;
            left:10%;
        }
        &__item
        {
            margin: 3px;
           >ul
           {
               >li
               {
                   margin-bottom: 18px;
                   text-overflow: ellipsis;
                   overflow: hidden;
                   white-space: nowrap;
                    >a
                    {
                        color: #0073bb;
                        font-weight: 500;
                        font-size: 14px;
                      
                        >svg
                        {
                            fill: #0073bb;
                            font-size: 24px;
                            margin-left: 6px;
        
                        }
                        &:hover
                        {
                            text-decoration: underline;
                        }
                    }
               }
           }
        }
      
    }
}

@media(max-width: 480px) {
    .yelp-paris {
        display: none;
    }
}

@media(max-width:576px) {
    .yelp-paris {
        display: none;
    }
}