.page-location {
	background-color: #f4f4f4;
	padding: 48px 0;
	margin-bottom: 96px;
	$page-location: &;

	.container {
		max-width: 1174px;
	}

	&__title {
		margin-bottom: 72px;
		text-align: center;

		h2 {
			font-size: 28px;
			line-height: 36px;
			color: #2b273c;
		}
	}

	&__item {
		display: flex;
		margin-bottom: 60px;

		&__icon {
			width: 40px;
			height: 40px;
			margin-left: 20px;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;

			}
		}

		&__text {
			&__title {
				margin-bottom: 8px;

				h5 {
					font-size: 16px;
					line-height: 22px;
					color: #2b273c;
					font-weight: 500;

				}
			}

			&__desc {
				margin-bottom: 16px;

				p {
					margin-bottom: 0;
					color: #757280;
					font-size: 15px;
					line-height: 25px;
					font-weight: 400;
				}
			}

			&__link {
				a {
					color: #00838f;
					font-size: 14px;
					line-height: 20px;
					font-weight: 500;

					svg {
						transform: rotate(180deg);
						fill: rgba(0, 131, 143, 1);
						margin-right: 12px;
					}
				}
			}
		}
	}

	&__info {
		margin-bottom: 30px;

		&__image {
			margin-bottom: 10px;
			display: flex;
			align-items: center;
			justify-content: center;
		}

		&__title {
			margin-bottom: 10px;

			h2 {
				font-size: 18px;
				font-weight: 600;
				line-height: 30px;
			}
		}

		&__desc {
			line-height: 25px;
		}
	}

	&__ads {
		margin-bottom: 30px;
		$ads: &;
		&__image {
			margin-bottom: 10px;
			display: flex;
			align-items: center;
			justify-content: center;
		}

		&__title {
			margin-bottom: 10px;

			h2 {
				font-size: 18px;
				font-weight: 600;
				line-height: 30px;
			}

		}

		&__desc {
			line-height: 25px;
		}

		&--text-center {
			text-align: center;
			#{$ads}__image {
				margin-bottom: 20px;
			}
			#{$ads}__title
			{
				margin-bottom: 25px;
				h2
				{
					font-size: 20	px;
				}
			}
		}
	}

	&--white {
		padding: 104px 0 0;
		background-color: transparent;

		#{$page-location}__item__text__desc {
			p {
				color: #2b273c;
			}
		}
	}
}
