.page-text-content {
	padding: 75px 0;

	.container {
		max-width: 1174px;
	}

	&__title {
		text-align: center;
		margin-bottom: 72px;

		h2 {
			color: #2b273c;
			font-size: 28px;
			line-height: 36px;
			font-weight: 500;
		}
	}

	.row {
		margin-bottom: 72px;
	}

	&__image {
        display: flex;
        align-items: center;
        justify-content: center;
    }

	&__text {
		&__title {
			margin-bottom: 32px;

			span {
				color: #bbbac0;
				font-size: 14px;
				line-height: 20px;
				font-weight: 500;
				span
				{
					display: inline-block;
					margin-left: 8px;
					padding: 0 10px;
					line-height: 24px;
					color: #fff;
					background: #05a882;
					border-radius: 12px;
				}
			}
		}

		&__title2 {
			margin-bottom: 16px;

			h3 {
				font-size: 24px;
				line-height: 30px;
				color: #2b273c;
				font-weight: 600;
			}
		}

		&__desc {
			margin-bottom: 24px;

			p {
				font-size: 14px;
				line-height: 25px;
                color: #757280;
                svg
                {
                    margin-left: 10px;
                }
			}
		}

		&__list {
			margin-bottom: 32px;

			ul {
				li {

					color: #2b273c;
					font-size: 16px;
					line-height: 22px;
					margin-bottom: 16px;

					&:last-child {
						margin-bottom: 0;
					}

					svg {
						fill: rgba(43, 39, 60, 1);
						margin-left: 12px;
					}
				}
			}
		}

		&__link {
			a {
				color: #00838f;
				font-size: 14px;
				line-height: 20px;
				font-weight: 500;

				svg {
					transform: rotate(180deg);
					fill: rgba(0, 131, 143, 1);
					margin-right: 12px;
				}
			}
		}
	}
}
