.empty-section
{
    &__wrapper
    {
        height: calc(100vh - 140px);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;
    }
    &__content
    {
        color: #2b273c;
        h2
        {
            font-size: 24px;
            line-height: 35px;
            font-weight: 600;
        }
        h4
        {
            font-size: 20px;
            line-height: 35px;
            font-weight: 600;
        }
    }
}