.header-mobile4 {
	display: none;
	height: auto;
	padding-top: 0px;
	background-color: #d32323;
	&__menu-mobail {
		&__top {
			display: flex;
			align-items: center;
			flex-direction: row-reverse;
			padding: 10px;
			&__logo {
				position: absolute;
				top: 7px;
				right: 50%;
				transform: translateX(50%);

				img {
					max-height: 28px;
				}
			}
			&__open-app {
				margin-left: auto;
				a {
					border: 1px solid #91151c;
					background-color: #bc1d25;
					border-radius: 3px;
					display: flex;
					align-items: center;
					color: #fff;
					padding: 0 10px;
					line-height: 28px;
					svg {
						fill: #fff;
					}
				}
			}
		}
	}
}

@media(max-width:749px) 
{
	.header-mobile4 {
		display: block;
	}
}
