.item-list-menu {
    margin-top: 48px;
    margin-bottom: 56px;

    .container-item-list-menu {
        max-width: 1144px;
        margin: 0 auto;
        padding: 0 15px;
    }

    &__title {
        margin-bottom: 6px;

        h4 {
            font-size: 20px;
            line-height: 26px;
            color: #2b273c;
            font-weight: 500;
        }
    }

    &__list {
        ul {
            li {
                line-height: 36px;

                a {
                    color: #757280;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 20px;

                    svg {
                        margin-left: 8px;
                    }

                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
    }

    &__text {
        a {
            color: #757280;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;

            &:hover {
                text-decoration: underline;
            }
        }
    }
}

@media(max-width: 480px) {
    .item-list-menu {
        // margin-top: 15px;
        // margin-bottom: 0px;

        .container-item-list-menu {
            max-width: 1144px;
            padding: 0 15px;
        }

        &__title {
            margin-bottom: 6px;

            h4 {
                font-size: 14px;
                line-height: 18px;
                color: #2b273c;
                font-weight: 400;
            }
        }

        &__list {
            ul {
                li {
                    line-height: 36px;

                    a {
                        color: #757280;
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 20px;

                        svg {
                            margin-left: 8px;
                        }

                        &:hover {
                            text-decoration: underline;
                        }
                    }
                }
            }
        }

        &__text {
            a {
                color: #757280;
                font-weight: 400;
                font-size: 14px;
                line-height: 20px;

                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
}