.national-ads {
	&__nav {
		border-bottom: 1px solid #e6e6e6;

		ul {
			display: flex;
			align-items: center;
			justify-content: center;

			li {
				a {
					display: block;
					line-height: 42px;
					border-bottom: 3px solid transparent;
					border-top: 3px solid transparent;
					padding: 0 12px;
					color: #000;
				}

				&.active {
					a {
						border-bottom-color: #d32323;
						font-weight: 600;
					}
				}

				&:hover {
					a {
						background-color: #f5f5f9;
						border-bottom-color: #d32323;
					}
				}
			}
		}
	}

	&__intro {
		padding-top: 120px;
		border-bottom: 1px solid #e6e6e6;
		background-color: #f5f5f5;

		&__image {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 615px;
			height: 423px;
		}

		&__content {
			h1 {
				color: #333;
				font-weight: 600;
				line-height: 40px;
				font-size: 30px;
				margin-bottom: 30px;
			}

			p {
				line-height: 25px;
				font-size: 13px;
			}

			&__row {
				display: flex;
				align-items: center;
			}

			&__btn {
				margin-left: 20px;

				a {
					display: block;
					line-height: 43px;
					background-color: #d32323;
					font-size: 16px;
					font-weight: 600;
					border-radius: 3px;
					padding: 0 15px;
					color: #fff;

					&:hover {
						background-color: #dc4f4f;
					}
				}
			}

			&__call {
				a {
					color: #333;
					display: flex;
					align-items: center;
					font-size: 14px;

					b {
						margin-right: 5px;
					}

					svg {
						fill: #666;
						margin-left: 5px;
					}
				}
			}
		}
	}

	&__info {
		padding: 100px 0 0;

		&__wrapper {
			padding: 0 30px 70px;
			border-bottom: 1px solid #e6e6e6;
		}

		&__title {
			margin-bottom: 48px;
			text-align: center;

			h3 {
				color: #333;
				font-size: 24px;
				font-weight: 400;
			}
		}

		&__item {
			display: flex;
			margin-bottom: 30px;

			&__image {
				flex: 0 0 120px;
				max-width: 120px;
			}

			&__content {
				flex: 0 0 calc(100% - 120px);
				max-width: calc(100% - 120px);
				padding-right: 24px;

				span {
					color: #666;
					font-size: 25px;
					font-weight: 600;
					margin-bottom: 10px;
					display: block;
				}

				p {
					font-size: 14px;
				}

				small {
					color: #999;
					font-size: 14px;
				}
			}
		}
	}

	&__service {
		padding: 100px 0 0;

		&__wrapper {
			padding-bottom: 70px;
			border-bottom: 1px solid #e6e6e6;
		}

		&__title {
			margin-bottom: 48px;
			text-align: center;

			h3 {
				color: #333;
				font-size: 24px;
				font-weight: 400;
			}
		}

		&__item {
			display: flex;
			flex-direction: column;
			margin-bottom: 30px;
			height: calc(100% - 30px);

			&__image {
				margin-bottom: 36px;
				display: flex;
				align-items: center;
				justify-content: center;

				img {
					border-radius: 8px;
				}
			}

			&__content {
				margin-bottom: 30px;
				text-align: center;

				h2 {
					font-size: 21px;
					margin-bottom: 15px;
				}

				p {
					margin-bottom: 0;
					font-size: 13px;
					line-height: 25px;
				}
			}

			&__btn {
				margin-top: auto;

				a {
					display: block;
					text-align: center;
					color: #666;
					background: #fff;
					border: 1px solid #ccc;
					border-radius: 3px;
					line-height: 36px;
					font-weight: 600;
					transition: 0.3s;
					font-size: 14px;

					&:hover {
						background-color: #f5f5f5;
					}
				}
			}
		}
	}

	&__video {
		padding: 100px 0 0;

		&__wrapper {
			padding-bottom: 70px;
			border-bottom: 1px solid #e6e6e6;
		}

		&__title {
			margin-bottom: 48px;
			text-align: center;

			h3 {
				color: #333;
				font-size: 24px;
				font-weight: 400;
			}
		}

		&__content {
			margin-bottom: 30px;

			p {
				padding-right: 18px;
				margin-bottom: 12px;
				border-right: 3px solid #d32323;
				color: #333;
				font-weight: 600;
				font-size: 14px;
				line-height: 30px;
			}

			span {
				padding-right: 18px;
				color: #666;
				font-size: 14px;
			}
		}

		&__video {
			position: relative;
			margin-bottom: 30px;
			display: flex;
			align-items: center;
			justify-content: center;

			svg {
				position: absolute;
				right: 50%;
				top: 50%;
				transform: translate(calc(50% + 3px), -50%);
				z-index: 5;
				width: 25px;
				height: 25px;
				fill: #fff;
				pointer-events: none;
			}

			&:before {
				content: '';
				position: absolute;
				right: 50%;
				top: 50%;
				transform: translate(50%, -50%);
				width: 98px;
				height: 98px;
				border-radius: 50%;
				z-index: 2;
				background-color: rgba(0, 0, 0, .45);
				transition: 0.2s;
				pointer-events: none;
			}

			&:hover {
				&:before {
					background-color: #d32323;
				}
			}
		}
	}

	&__call {
		padding: 100px 0;

		&__content {
			color: #333;

			h3 {
				font-size: 20px;
				font-weight: 600;
				margin-bottom: 35px;
			}

			p {
				font-size: 13px;
			}
		}

		&__row {
			display: flex;
			align-items: center;
		}

		&__btn {
			margin-left: 20px;

			a {
				display: block;
				line-height: 43px;
				background-color: #d32323;
				font-size: 16px;
				font-weight: 600;
				border-radius: 3px;
				padding: 0 15px;
				color: #fff;

				&:hover {
					background-color: #dc4f4f;
				}
			}
		}

		&__call {
			a {
				color: #333;
				display: flex;
				align-items: center;
				font-size: 14px;

				b {
					margin-right: 5px;
				}

				svg {
					fill: #666;
					margin-left: 5px;
				}
			}
		}
	}
}

@media(max-width: 1300px) {
	.national-ads {
		&__intro {
			&__image {
				width: 100%;
				height: auto;
			}
		}


	}
}

@media(max-width: 992px) {
	.national-ads {
		&__intro {
			padding-top: 30px;

			&__content {
				text-align: center;
				margin-bottom: 30px;

				&__row {
					justify-content: center;
				}
			}
		}

		&__call {
			&__content {
				text-align: center;
			}

			&__row {
				justify-content: center;
			}
		}
	}
}

@media(max-width: 768px) {
	.national-ads {
		&__nav {
			display: none;
		}

		&__info {
			padding: 40px 0 0;

			&__wrapper {
				padding-bottom: 10px;
			}
		}

		&__service {
			padding: 40px 0 0;

			&__wrapper {
				padding-bottom: 10px;
			}
		}

		&__video {
			padding: 40px 0 0;

			&__wrapper {
				padding-bottom: 10px;
			}
		}

		&__call {
			padding: 40px 0;
		}
	}
}
