.sub-header-mobile3
{
    display: none;
    &__menu-mobail {
       
        &__bottom {
            padding: 15px;
            border-bottom: 30px solid #f5f5f5;

            &__item {
                padding-bottom: 15px;

                a {
                    display: flex;
                    align-items: center;
                    flex-direction: column;

                    img {
                        width: 36px;
                        height: 36px;
                    }

                    span {
                        line-height: 18px;
                        font-size: 12px;
                        color: black;
                    }

                }
            }
        }

    }
}

@media(max-width:749px)
{
    .sub-header-mobile3
{
    display:block;
}
}