.app-mobail {
    padding-top: 72px;
    padding-bottom: 72px;

    &__title {
        margin-bottom: 24px;
        text-align: center;


        h2 {
            font-weight: 500;
            margin-bottom: 6px;
            font-size: 21px;
            line-height: 26px;
            color: #d32323;
        }
    }

    &__item {
        display: block;
        padding: 30px;
        border: 1px solid #e6e6e6;
        border-radius: 4px;
        text-align: center;

        &:hover {
            border: 1px solid #0073bb;
            box-shadow: 0 6px 12px rgba(0, 0, 0, .1);

            h4 {
                color: #0073bb;

            }

        }

        a {}

        &__title {



            h4 {
                color: #333;
                font-weight: 500;
                margin-bottom: 6px;
                font-size: 14px;
                line-height: 18px;

            }




        }

        &__image {
            img {}

        }
    }

    &__button {
        display: none;
        background-color: #f5f5f5;
        padding: 24px 12px 36px 12px;

        a {
            display: block;
            color: #fff;
            background: #0097ec;
            border: 1px solid #0097ec;
            text-align: center;
            line-height: 57px;
            font-size: 16px;
            font-weight: 500;
            border-radius: 4px;

        }
    }
}

@media(max-width: 480px) {
    .app-mobail {
        padding-top: 0px;
        padding-bottom: 0px;
        border-top: 1px solid #ccc;

        &__title {
            display: none;
        }

        &__item {
            display: none;

        }

        &__button {
            display: block;
        }
    }
}

@media(max-width:576px) {
    .app-mobail {
        padding-top: 0px;
        padding-bottom: 0px;
        border-top: 1px solid #ccc;

        &__title {
            display: none;
        }

        &__item {
            display: none;

        }

        &__button {
            display: block;
        }
    }
}