.adpartenrs-partners
{
    position: relative;
    padding: 30px 0;
    margin-bottom: 80px;
    &__wrapper
    {
        padding: 0 15px;
    }
    &__title
    {
        text-align: center;
        margin-bottom: 40px;
        h3
        {
            font-size: 16px;
            font-weight: 600;
            color: #FF122C;
            margin-bottom: 5px;
        }
        p
        {
            color: #6B6E81;
            font-size: 14px;
        }
        a
        {
            color: #14A5D7;
        }
    }
    &__item
    {
        box-shadow: 0 5px 10px 0 rgba(0,0,0,0.2);
        height: 200px;
        position: relative;
        transition: 0.3s;
        margin-bottom: 30px;
        border-radius: 20px;
        background-color: #fff;
        &__title
        {
            height: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color:#ff122d;
            border-radius: 20px 20px 0 0;
            span
            {
                display: block;
                text-align: center;
                line-height: 40px;
                color: #fff;
                font-size: 16px;
            }
        }
        &__image
        {
            height: 160px;
            display: flex;
            align-items: center;
            justify-content: center;
            img
            {
                max-width: 145px;
            }
        }
        
        a
        {
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            z-index: 5;
        }
        &:hover
        {
            box-shadow: 0 10px 20px 0 rgba(0,0,0,0.2);
            span
            {
                text-decoration: underline;
            }
        }
    }
}