.write-areviews
{
    margin:42px 0;
    &__title
    {
        margin-bottom: 30px;
        h2
        {
            color: #d32323;
            font-size: 21px;
            font-weight: 500;
        }
    }
    &__item
    {
        border: 1px solid #e6e6e6;
        // border-top-right-radius: 3px;
        // border-bottom-right-radius: 3px;
        border-radius: 3px;
        display: flex;
        justify-content: center;
        position: relative;
        margin-bottom: 24px;
        
        &__image
        {
            flex: 0 0 152px;
            max-width: 152px;
            height: 157px;
            border-radius: 0 3px 3px 0;
            position: relative;
            overflow: hidden;
            img
            {
               height: 100%;
               width: 100%;
               object-fit: cover;
            }
        }
        &__text
        {
            flex: 0 0 calc(100% - 152px);
            max-width:calc(100% - 152px) ;
            padding-right: 24px;
            margin: auto 0;
            &__title
            {
                margin-bottom: 6px;
                h3
                {
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    a
                    {
                        color: #0073bb;
                        font-size: 16px;
                        &:hover
                        {
                            text-decoration: underline;
                        }
                    }
                }
            }
            &__desc
            {
                p
                {
                    color: #333;
                    font-size: 14px;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                }
            }
            &__star
            {
                &__icon {
                    display: flex;
                    align-items: center;
                    flex-direction: row-reverse;
                    justify-content: flex-end;

                    input {
                        display: none;
                        &:checked
                        {
                            +label
                            {
                                &[data-star="1"] {
                                    background-position: 0 -64px;
                                    ~label {
                                        background-position: 0 -64px;
                                    }
                                }
        
                                &[data-star="2"] {
                                    background-position: 0 -128px;
                                    ~label {
                                        background-position: 0 -128px;
                                    }
                                }
        
                                &[data-star="3"] {
                                    background-position: 0 -192px;
                                    ~label {
                                        background-position: 0 -192px;
                                    }
                                }
        
                                &[data-star="4"] {
                                    background-position: 0 -256px;
                                    ~label {
                                        background-position: 0 -256px;
                                    }
                                }
        
                                &[data-star="5"] {
                                    background-position: 0 -288px;
                                    ~label {
                                        background-position: 0 -288px;
                                    }
                                }
                            }
                        }
                    }

                    label {
                        width: 32px;
                        height: 32px;
                        background-position: 0 0;
                        background-image: url(../img/star.png);
                        background-repeat: no-repeat;
                        margin-left: 2px;
                        cursor: pointer;


                        &[data-star="1"] {
                            &:hover {
                                background-position: 0 -64px;

                                ~label {
                                    background-position: 0 -64px;
                                }
                            }
                        }

                        &[data-star="2"] {
                            &:hover {
                                background-position: 0 -128px;

                                ~label {
                                    background-position: 0 -128px;
                                }
                            }
                        }

                        &[data-star="3"] {
                            &:hover {
                                background-position: 0 -192px;

                                ~label {
                                    background-position: 0 -192px;
                                }
                            }
                        }

                        &[data-star="4"] {
                            &:hover {
                                background-position: 0 -256px;

                                ~label {
                                    background-position: 0 -256px;
                                }
                            }
                        }

                        &[data-star="5"] {
                            &:hover {
                                background-position: 0 -288px;

                                ~label {
                                    background-position: 0 -288px;
                                }
                            }
                        }
                    }
                    
                }
            }
            &__close
            {
                position: absolute;
                left: 12px;
                top: 12px;
                cursor: pointer;
                svg
                {
                    fill: rgba(204,204,204,1);;
                }
            }
            &__show-more
            {
                text-align: center;
                display: flex;
                justify-content: center;
                a
                {
                    display: flex;
                    align-items: center;
                    color: #0073bb;
                    &:hover{
                        text-decoration: underline;
                    }
                    svg
                    {
                    fill: #0073bb;
                    
                    }
                    
                }
            }
       
        }
    }
}



@media(max-width:480px)
{
    .write-areviews
    {
        &__item
        {
            margin-bottom: 15px;
            &__image
            {
                flex: 0 0 90px;
                max-width: 90px;
                margin-left:3px;
                img
                {
                    width: 100%;
                    height: 112px;
                    
                }
            }
            &__text
            {
                flex: 0 0 calc(100% - 90px);
                max-width: calc(100% - 90px);
                text-align: center;
                &__close
                {
                    left: 4px;
                    top: 4px;
                }
                &__star
                {
                    text-align: center;
                    display: flex;
                    justify-content: center;
                }
                &__title
                {
                    h3
                    {
                        a
                        {
                            font-size:14px;
                        }
                    }
                }
                &__desc
                {
                    p
                    {
                        font-size: 11px;
                    }
                }
               
            }
        }
    }
}