.first-review
{
    background-color: #f5f5f5;
    padding: 36px 0;
&__item
{
  
    &__title
    {
        margin-bottom: 18px;
        h1
        {
            color: #333;
           padding: 6px 0;
            font-size: 30px;
            font-weight: 500;
        }
    }
    &__text
    {
        display: flex;
        align-items: center;
        margin-bottom: 24px;
        
        p
        {
            margin-bottom: 0;
            font-size: 16px;
            line-height: 24px;
            color: #333;

            a
            {
                color: #0073bb;
                font-size: 16px;
                &:hover{
                    text-decoration: underline;
                }

            }
        }
    }
    &__search
    {
        display: flex;
        align-items: center;
        width: 100%;
        &__find
        {
            background-color: #fff;
            border: 1px solid #e6e6e6;
            flex: 0 0 calc(50% - 24px);
            max-width: calc(50% - 24px);        
            border-radius: 3px;
            line-height:24px;
            display: flex;
            margin-left:6px;

            label
            {
                padding-right:5px;
                font-size: 14px;
                font-weight: 500;
                flex: 0 0 65px;
                max-width: 65px;
                margin-bottom: 0;


            }
            input
            {
                border: none;
                font-size: 14px;
                flex: 0 0 calc(100% - 65px);
                max-width:calc(100% - 65px) ;


            }
        }
        &__near
        {
            background-color: #fff;
            border: 1px solid #e6e6e6;
            flex: 0 0 calc(50% - 24px);
            max-width: calc(50% - 24px);        
            border-radius: 3px;
            line-height:24px;
            display: flex;
            margin-left: 6px;


            label
            {
                padding-right: 5px;
                font-size: 14px;
                font-weight: 500;
                flex: 0 0 60px;
                max-width: 60px;
                margin-bottom: 0;

            }
            input
            {
                border: none;
                flex: 0 0 calc(100% - 60px);
                max-width:calc(100% - 60px) ;
                font-size: 14px;
                
            }
        }
        button
        {
            color: #fff;
            background: #d32323;
            border: 1px solid #d32323;
            flex: 0 0 36px;
            max-width: 36px;
            line-height: 24px;
            border-radius: 3px;
            text-align: center;
            &:hover
            {
                background-color: #dc4f4f;
            border: 1px solid #dc4f4f;


            }
            svg
            {
                fill: #fff;
            }
        }
    }
}
    &__image
    {
        img
        {
            width: 260px;
            height: 160px;
        }

    }
  
}

@media(max-width:767px)
{
    .first-review
    {
        &__image
        {
            text-align:center;
        }
    }
}

@media(max-width:480px)
{
    .first-review
    {
        &__item
        {
            &__title
            {
                h1
                {
                    font-size: 16px;
                }
            }
        }
    }
}