.header-mobile2
{
    margin-top:6px ;
    display: none;
    &__menu-mobail {
        &__top {
            display: flex;
            align-items: center;
            padding-bottom: 10px;
            position: relative;
            flex-direction: row-reverse;

            &__icon {
                margin-right: 25px;
                position: relative;

                svg {
                    fill: rgba(153, 153, 153, 1);
                    width: 24px;
                    height: 24px
                }


            }

            &__icon-search {

                svg {
                    fill: rgba(153, 153, 153, 1);
                }
            }

            &__logo {
                transform: translateX(50%);
                position: absolute;
                right: 50%;

                img {
                    max-height: 26px;
                }

            }

            &__open-app {
                margin-left: auto;

                a {
                    border: 1px solid #91151c;
                    background-color: #bc1d25;
                    border-radius: 3px;
                    display: flex;
                    align-items: center;
                    color: #fff;
                    padding: 0 10px;
                    line-height: 30px;

                    svg {
                        fill: #fff;
                    }
                }
            }
        }

        &__bottom {
            margin: 0 -10px;


            >ul {
                display: flex;
                width: 100%;
                align-items: center;
                position: relative;


                >li {
                    width: 33.33%;
                    flex: 0 0 33.33%;
                    border: 1px solid #e6e6e6;


                    &:first-child {
                        border-right: none;
                    }

                    &:last-child {
                        border-left: none;
                    }

                    a {
                        display: flex;
                        align-items: center;
                        font-weight: 500;
                        padding: 0 12px;
                        background-color: #fafafa;
                        font-size: 12px;
                        color: black;
                        line-height: 40px;

                        &:hover {
                            color: #0073bb;
                            fill: #0073bb;
                        }



                        svg {
                            margin-left: 5px;
                        }
                    }

                    >ul {
                        columns: 2;
                        visibility: hidden;
                        opacity: 0;
                        background-color: #fff;
                        position: absolute;
                        right: 0;
                        width: 100%;
                        top: 100%;
                        z-index: 2;
                        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);

                        >li {
                            >a {
                                background-color: #fff;
                                font-size: 12px;
                                color: black;
                                line-height: 40px;
                            }
                        }

                    }


                    &:hover {
                        >ul {
                            opacity: 1;
                            visibility: visible;
                        }
                    }

                }
            }
        }

    }
}

@media(max-width:749px) {
    .header-mobile2 {
        display: block;
      
    }
}

@media(max-width:576px) {
    .header-mobile2 {
        // padding: 10px 10px 0;
        border-bottom: none;

        &__menu {
            display: none;
        }

        &__menu-bottom {
            display: none;
        }

        &__menu-mobail {
            display: block;
        }
    }
}

@media(max-width: 480px) {
    .header-mobile2 {
        // padding: 10px 10px 0;
        border-bottom: none;

        &__menu {
            display: none;
        }

        &__menu-bottom {
            display: none;
        }

        &__menu-mobail {
            display: block;
            &__top
            {
                &__icon
                {
                    margin-right: 6px;
                }
            }
            &__bottom
            {
                >ul {
                    width: 100%;    
                    >li {
                        width:auto;
                        a {
                            padding: 0 1px;
                            font-size: 11px;
                            svg {
                                margin-left:3px;
                            }
                        }
    
                    }
                }

            }
        }
    }
}