.header-mobile3
{
    display: none;
    height: auto;
    padding-top: 0px;
    background-color: #d32323;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 999;
    &__menu-mobail {
       
        &__top {
            display: flex;
            flex-direction: row-reverse;
            align-items: center;
            padding: 6px 10px;
            position: relative;
            &__icon {
                margin-right: 25px;
                position: relative;

                svg {
                    fill: #fff;
                    width: 24px;
                    height: 24px;
                    &:last-child
                    {
                        display: none;
                    }
                }
                &--open
                {
                    svg
                    {
                        display: none;
                        &:last-child
                        {
                            display: block;
                        }
                    }
                }


            }

            &__logo {
                position: absolute;
                top: 7px;
                right: 50%;
                transform: translateX(50%);

                img {
                    max-height: 26px;
                }

            }

            &__open-app {
                margin-left: auto;
                a {
                    border: 1px solid #91151c;
                    background-color: #bc1d25;
                    border-radius: 3px;
                    display: flex;
                    align-items: center;
                    color: #fff;
                    padding: 0 10px;
                    line-height: 30px;

                    svg {
                        fill: #fff;
                    }
                }
            }
        }
        &__bottom
        {
            position: relative;
            padding: 10px;
            &__search
            {
                background: #fff;
                border-radius: 4px;
                display:flex;
                align-items: center;
                padding: 5px;
                label
                {
                    margin-bottom:0 ;
                    line-height: 20px;
                    svg

                    {
                        fill: #666;
                        font-size: 14px;
                        margin-left:5px ;
                    }

                }
                input
                {
                    border: none;
                    width: 100%;
                    line-height: 22px;

                   

                
                }


            }
        }
    }
    &__menu
    {
        position: fixed;
        right: 0;
        bottom: 0;
        left: 0;
        top: 44px;
        z-index: 99;
        background-color: #fff;
        padding: 0 12px;
        display: none;
        >ul
        {
            margin-bottom: 30px;
            >li
            {
                line-height: 50px;
                border-bottom: 1px solid #e6e6e6;
                >a
                {
                    color: #333;
                    font-size: 14px;
                    svg
                    {
                        fill: rgb(102, 102, 102);
                        margin-left: 10px;
                    }
                }
                &.active
                {
                    a
                    {
                        color: #d32323;
                        svg
                        {
                            fill: #d32323;
                        }
                    }
                }
            }
        }
        &__links
        {
            ul
            {
                display: flex;
                align-items: center;
                justify-content: center;
                li
                {
                    margin-left: 20px;
                    &:last-child
                    {
                        margin-left: 0;
                    }
                    a
                    {
                        color: #0073bb;
                    }
                }
            }
        }
    }
}

@media(max-width:749px) {
    .header-mobile3 {
        display: block;
    }
}

