.page-video {
    
	margin-bottom: 145px;
	&--no-seprate
	{
		margin-bottom: 0;
	}
    .container {
        max-width: 1174px;
    }
	&__title {
		margin-bottom: 32px;

		h2 {
			font-size: 24px;
            line-height: 36px;
            font-weight: 600;
			color: #2b273c;

		}
		p {
			font-weight: 400;
			font-size: 13px;
			line-height: 20px;
			margin-top: 15px;
			color: #666;
		}
	}

	&__desc {
		
		margin-bottom: 10px;
		p {
			margin-bottom: 0;
			color: #757280;
			font-weight: 400;
			font-size: 14px;
			line-height: 20px;
			font-style: italic;
		}
		span {
			font-weight: 400;
			font-size: 13px;
			line-height: 20px;
		}
	}

	&__name {

		p {
			margin-bottom: 0;
			color: #2b273c;
			font-size: 14px;
			line-height: 20px;
			font-weight: 500;
		}
	}

	&__location {
		margin-bottom: 48px;

		p {
			margin-bottom: 0;
			color: #757280;
			font-weight: 400;
			font-size: 14px;
			line-height: 20px;
		}
	}

	&__img-video {
		position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
		img {
            width: 100%;
        }

		svg {
			position: absolute;
			bottom: 50%;
			right: 50%;
			fill: rgba(255, 255, 255, 1);
			z-index: 999;
		}

	}

}
