.login
{
    margin-bottom:36px;
 
    &__item
    {
        width: 300px;
        margin:144px 30px 30px 30px;

        &__title
        {
            margin-bottom: 6px;
            text-align: center;
            h2
            {
                color: #d32323;
                
                line-height: 28px;
                font-size:22px;

            }
        }
        &__subtitle
        {
           justify-content: center;
            display: flex;
            align-items: center;
            margin-bottom: 12px;
           
            p
            {
                margin-bottom: 0;
                color: #333;
                font-weight:500;
                margin-left: 5px;
                font-size: 14px;
                line-height: 17px;
                

            }
            a
            {
                color: #0073bb;
                cursor: pointer ;
                font-size: 14px;
                line-height: 17px;

                &:hover
                {
                    text-decoration: underline;
                }

            }
        }
        .form-check
        {
            display: flex;
            margin-bottom:30px;
            justify-content: center;
            padding-left: 0;
          
            text-align: center;

            label
            {
                text-align: center;
                
                a
                {
                    color: #0073bb;
                    padding: 0 5px;
                    cursor: pointer ;
                    &:hover
                    {
                        text-decoration: underline;
                    }

                }
            }
          
        }
        .form-check-input
        {
            position: static;
            margin-top: 0;
            margin-right: 0;
        }
        .form-check-label
        {
            padding-right: 25px;
        
        }
        &__form
        {
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;
            &__facebook
            {
                margin-bottom: 12px;
               
                a
                {
                    color: #fff;
                     background: #1877f2;
                     border: 1px solid #1877f2;
                     line-height: 41px;
                     display: block;
                     text-align: center;
                     font-size: 16px;
                     font-weight: 500px;
                     cursor: pointer ;
                     border-radius: 3px;
                     transition: all 0.5s;
                     &:hover
                     {
                        background-color: #4692f5;
                     }
                }
            }
            &__google
            {
                margin-bottom: 12px;
                a
                {
                    color: #666;
                    background: #fff;
                    border: 1px solid #ccc;
                     line-height: 41px;
                     display: block;
                     text-align: center;
                     font-size: 16px;
                     font-weight: 500px;
                     cursor: pointer ;
                     border-radius: 3px;
                     transition: all 0.5s;

                  
                }
            }
            &__apple
            {
                margin-bottom: 24px;
                a
                {
                    color: #fff;
                    background: #000;
                    border: 1px solid #000;
                     line-height: 41px;
                     display: block;
                     text-align: center;
                     font-size: 16px;
                     font-weight: 500px;
                     cursor: pointer ;
                     border-radius: 3px;
                     transition: all 0.5s;

                     &:hover
                     {
                        background-color: #333;
                     }
                  
                }
            }
          
          
        }
        &__title-or
        {
            position: relative;
            margin-bottom: 24px;
            display: flex;
            justify-content: center;

            &:before
            {
                position: absolute;
                content: "";
                left: 0;
                right: 0;
                top: 50%;
                z-index:1;
                border: 1px solid #ccc;
            }
            span
            {
                color: #666;
                position: relative;
                background-color: #fff;
                text-align: center;
                display: flex;
                justify-content: center;
                padding: 0 15px;
                z-index:2;
                

            }
        }
      
        .form-group
        {
            // line-height: 30px;
            margin-bottom: 0;
        
            input
            {
                color: #495057;
                background-color: #fff;
                border: 1px solid #ced4da;
                border-radius: 3px;
                line-height: 30px;
                margin-bottom: 9px;
                margin-top: 0;
                font-size: 14px;
                &:focus
                {
                    box-shadow: 0 0 4px rgba(0,115,187,0.3);
                    border-color: #0073bb;
                }
            }
           
         

        }
        .Forgot-password
        {
            margin-bottom: 12px;
           a
            {
                color: #0073bb;
                font-size: 12px;
                &:hover
                {
                    text-decoration: underline;
                }

            }
        
        }
        .btn-primary
        {
            color: #fff;
            background: #d32323;
            border: 1px solid #d32323;
            width: 300px;
            line-height:29px;
            transition: all 0.5s;
            &:hover
            {
                background-color: #dc4f4f;
            }
            &:focus
            {
              
                background-color: #be2020;
                box-shadow: none;
                outline: none;
               
            }
            &.btn-primary:not(:disabled):not(.disabled).active:focus,
            &.btn-primary:not(:disabled):not(.disabled):active:focus,
            &.show > .btn-primary.dropdown-toggle:focus {
                box-shadow: none
            }
        
         
            &:not(:disabled):not(.disabled).active,
            &:not(:disabled):not(.disabled):active
            {
                color: #fff;
                background-color: #dc4f4f;
                border: 1px solid #d32323;
            }

        }
        &__sub-form
        {
         margin-top: 12px;
            display: flex;
            
            margin-bottom: 12px;
            p
            {
                margin-bottom: 0;
                color: #333;
                font-weight:500;
                margin-left: 5px;
                

            }
            a
            {
                color: #0073bb;
                cursor: pointer ;
                &:hover
                {
                    text-decoration: underline;
                }

            }
        }
      
    }
    &__image
    {
        margin-top: 117px;
        img
        {

        }
    }

}

@media(max-width:576px)
{

    .login
    {
       margin-bottom: 33px;
        &__item
        {
            // margin: 30px 30px 30px 66px;
            margin: 30px auto;
           
        }
    }
}
@media(max-width:480px)
{
    .login
    {
        &__item
        {
            width:auto;
            .btn-primary
            {
                width: 100%;
            }
        }
    }
}
