.page-learn
{
    margin-bottom: 145px;
    .container {
        max-width: 1174px;
    }
    &__title
    {
        margin-bottom: 72px;
        text-align: center;
        h2
        {
            font-size: 28px;
             line-height: 36px;
             color: #2b273c;

        }
    }
    &__item
    {
        
        &__image
        {
         margin-bottom: 16px;
            height: 232px;
            img
            {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &__title
        {
            margin-bottom: 8px;
            h5
            {
                font-size: 16px;
                line-height: 22px;
                color: #2b273c;
                font-weight: 500;
            }
        }
        &__link
        {
            a
            {
                color: #00838f;
                font-size: 14px;
                line-height: 20px;
                font-weight: 500;
                svg
                {
                    transform: rotate(180deg);
                    fill: rgba(0,131,143,1);
                    margin-right: 12px;
                }
            }
        }
        
    }
 
}

@media(max-width:992px)
{
    .page-learn
    {
        &__item
        {
            margin-bottom: 32px;
            text-align: center;
            display: flex;
            flex-direction: column;
            justify-content: center;
            &__image
            {
                width: 768px;
                height: 512px;

            }
        }
    }
}